export class TableDetailViewModel
{
  /// <summary>
///  結構描述
/// <summary>
public schemaname: string;
/// <summary>
///  資料表
/// <summary>
public tablename: string;
/// <summary>
///  資料表說明
/// <summary>
public tablecname: string;
/// <summary>
///  欄位說明
/// <summary>
public columncname: string;
/// <summary>
///  欄位編號
/// <summary>
public column_id: number;

/// <summary>
///  檔案欄位索引
/// <summary>
public col_index: number;

/// <summary>
///  欄位名稱
/// <summary>
public columnname: string;
/// <summary>
///  資料型態
/// <summary>
public data_type: string;

/// <summary>
///  是否主鍵
/// <summary>
public is_pk: number;
/// <summary>
///  叢集唯一
/// <summary>
public is_uni_cluster: number;
/// <summary>
///  叢集非唯一
/// <summary>
public is_cluster: number;
/// <summary>
///  空值
/// <summary>
public is_nullable:boolean;
/// <summary>
///  空值
/// <summary>
public is_nullable_str:string;
/// <summary>
///  預設值
/// <summary>
public default_value: string;

/// <summary>
///  計算欄位
/// <summary>
public computecolumn: string;
/// <summary>
///  自動累加
/// <summary>
public is_identity:boolean;
/// <summary>
///  自動累加
/// <summary>
public is_identity_str:string;
/// <summary>
///  備註說明
/// <summary>
public memo: string;

/// <summary>
///  資料表全名
/// <summary>
public tablefullname: string;

/// <summary>
///  ID檢核
/// <summary>
public id_check: boolean;
/// <summary>
///  刪除註記
/// <summary>
public del_flag: boolean;
/// <summary>
///  資料欄起始位置
/// <summary>
public col_start_loc: number;
/// <summary>
///  資料欄位長度
/// <summary>
public col_length: number;
/// <summary>
///  欄位型態主鍵
/// <summary>
public column_type_key: string;
/// <summary>
///  歷史欄位註記
/// <summary>
public history_column_flag: boolean;
/// <summary>
///  日期轉換格式
/// <summary>
public date_convert_format: string;

/// <summary>
///  欄位型態
/// <summary>
public column_type: string;
/// <summary>
///  匯出是否加入正負號
/// <summary>
public is_sign:boolean;

/// <summary>
  ///  檢核類別
  /// <summary>
  public check_type: string;
  /// <summary>
  ///  檢核類別顏色
  /// <summary>
  public code_check_color:string;
}



