import { ConnViewModel } from "./ConnViewModel";
import { String } from "../../Model/String";

export class ConnFtpViewModel extends ConnViewModel {
  /// <summary>
  ///  伺服器名稱
  /// </summary>

  public server_name: string;

  /// <summary>
  ///  使用者名稱
  /// </summary>
  public user_name: string;

  /// <summary>
  ///  port
  /// </summary>
  public port: number;

  /// <summary>
  ///  Passive Mode
  /// </summary>
  public passive_mode: boolean;

  /// <summary>
  ///  Primary Key
  /// </summary>
  public primary_key: string;

  /// <summary>
  ///  PrivateKeyPassphrase
  /// </summary>
  public primary_key_passphrase: string;

  public get_conn_string(): string {
    let conn_string: string =
      "ServerName={0};ServerUserName={1};ServerPort={2}";
    conn_string = String.Format(
      conn_string,
      this.server_name,
      this.user_name,
      this.port
    );
    if (this.passive_mode === true) conn_string += ";UsePassive=1";
    if (this.primary_key > "") {
      conn_string += ";Privatekey=" + this.primary_key;
      if (this.primary_key_passphrase > "")
        conn_string += ";primary_key_passphrase=" + this.primary_key_passphrase;
    }

    return conn_string;
  }
}



