export class GlossaryViewModel {
  /// <summary>
  ///  中文名稱
  /// <summary>
  public cht_name: string;
  /// <summary>
  ///  英文名稱
  /// <summary>
  public eng_name: string;
  /// <summary>
  ///  英文名稱長度
  /// <summary>
  public eng_name_length: number;
  /// <summary>
  ///  資料型態
  /// <summary>
  public data_type: string;
  /// <summary>
  ///  資料字典類型
  /// <summary>
  public glossary_type: string;
   /// <summary>
  ///  資料字典類型說明
  /// <summary>
  public glossary_type_desc: string;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;
}



