import { ProgramPageViewModel } from "./ProgramPageViewModel";
import { MenuViewModel } from "./MenuViewModel";

export class ProgramPageMenuViewModel {
  /// <summary>
  ///  程式頁面名稱
  /// <summary>
  public page_name: string;
  /// <summary>
  ///  選單編號
  /// <summary>
  public menu_no: string;
  /// <summary>
  ///  顯示順序編號
  /// <summary>
  public display_order_no: number;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;

  public ProgramPageViewModel: ProgramPageViewModel;
  public MenuViewModel: MenuViewModel;
}



