export class FileBrowserViewModel {
  /// <summary>
  ///  檔案名稱
  /// <summary>
  public filename: string;
  /// <summary>
  ///  變更檔案名稱
  /// <summary>
  public rename_filename: string;
  /// <summary>
  ///  副檔名
  /// <summary>
  public ext: string;
  /// <summary>
  ///  檔案空間大小(byte)
  /// <summary>
  public file_size: number;
  /// <summary>
  ///  檔案空間大小
  /// <summary>
  public file_size_desc: string;
  /// <summary>
  ///  檔案全名(含路徑)
  /// <summary>
  public filefullname: string;

  /// <summary>
  ///  檔案路徑
  /// <summary>
  public filepath: string;

  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
}



