import { UserViewModel } from '../ViewModel/UserViewModel';
import { SingleModelResponse } from './SingleModelResponse';
import { PermissionViewModel } from '../ViewModel/PermissionViewModel';


export class UserResponse extends SingleModelResponse<UserViewModel> {
      Model: UserViewModel;
      constructor(obj?: any)
      {
        super(obj);
        this.Message= obj.Message ;
        this.DidError=obj.DidError;
        this.ErrorMessage=obj.ErrorMessage ;
        if(this.DidError===false)
        {
          this.Model= new UserViewModel();
          this.Model.userName=obj.Model.userName;
          this.Model.password=obj.Model.password;
          this.Model.Email=obj.Model.Email;
          this.Model.checked=false;
          this.Model.login_log_seq=obj.Model.login_log_seq;
          this.Model.roleName=[];
          this.Model.PermissionViewModel=[];
          if(obj!=null)
          {
            if (obj.Model.roleName != null) {
              obj.Model.roleName.forEach(element => {
                this.Model.roleName.push(element);
              });
            }
            if (obj.Model.permissionViewModel != null) {
              obj.Model.permissionViewModel.forEach(element => {
                let p = new PermissionViewModel();
                p.permission_no = element.permission_no;
                p.permission_desc = element.permission_desc;
                p.permission_func_desc = element.permission_func_desc;
                p.creator = element.creator;
                p.create_time = element.create_time;
                p.modifier = element.modifier;
                p.last_update_time = element.last_update_time;
                this.Model.PermissionViewModel.push(p);
              });

            }
          }
        }
      }
  }





