export class DirectoryBrowserViewModel {
  /// <summary>
  ///  資料夾名稱
  /// <summary>
  public directory_name: string;
  /// <summary>
  ///  檔案路徑
  /// <summary>
  public filepath: string;

  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;

  /// <summary>
  ///  檔案數
  /// <summary>
  public file_cnt:number;
  /// <summary>
  ///  子資料夾數
  /// <summary>
  public sub_directory_cnt:number;
  /// <summary>
  ///  是否有檔案
  /// <summary>
  public has_file:boolean;
/// <summary>
  ///  是否有檔案顏色
  /// <summary>
  public has_file_color:string;

}



