import { SingleModelResponse } from "./SingleModelResponse";
import { ConnViewModel } from "../ViewModel/ConnViewModel";

export class ConnResponse extends SingleModelResponse<ConnViewModel> {
  Model: ConnViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new ConnViewModel();
      this.Model.remote_conn_id = obj.Model.remote_conn_id;
      this.Model.db_provider = (obj && obj.Model.db_provider) || null;
      this.Model.conn_string = obj.Model.conn_string;
      this.Model.pwd = (obj && obj.Model.pwd) || null;
      this.Model.creator = obj.Model.creator;
      this.Model.create_time = obj.Model.create_time;
      this.Model.modifier = obj.Model.modifier;
      this.Model.last_update_time = obj.Model.last_update_time;
      this.Model.conn_type_key = obj.Model.conn_type_key;
      this.Model.conn_type = obj.Model.conn_type;
    }
  }
}



