import { ApiUrlViewModel } from "../ViewModel/ApiUrlViewModel";
export class ApiUrlRepository {
  data_list: ApiUrlViewModel[];
  constructor() {
    this.data_list = [];
  }
  Add(item: ApiUrlViewModel) {
    this.data_list.push(item);
  }

  AddList(items: ApiUrlViewModel[]) {
    this.data_list = [];
    items.forEach(item => {
      this.data_list.push(item);
    });
  }
  Del(id: string) {
    let index = this.data_list.findIndex(d => d.id === id);
    this.data_list.splice(index, 1); //remove element from array
  }
  List(keyword: string, order_type: boolean) {
    if (keyword > "") {
      this.data_list = this.data_list.filter(item => {
        return (
          item.disp_name.toLowerCase().indexOf(keyword.toLowerCase()) > -1 ||
          item.url.toLowerCase().indexOf(keyword.toLowerCase()) > -1
        );
      });
    }
    if (order_type) return this.data_list.sort();
    else
      return this.data_list.sort(
        (itema, itemb) => (itema.id > itemb.id ? -1 : 1)
      );
  }
  Update(item: ApiUrlViewModel) {
    if (item.is_default === true) {
      this.data_list.forEach(p => {
        p.is_default = false;
      });
    }
    let index = this.data_list.findIndex(d => d.id === item.id);
    this.data_list[index] = item;
  }
}



