import {StringResponse} from '../../Model/Response/StringResponse';
import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { CodeCheckListResponse } from "../../Model/Response/CodeCheckListResponse";
import { CodeCheckResponse } from "../../Model/Response/CodeCheckResponse";
import { CodeCheckViewModel } from '../../Model/ViewModel/CodeCheckViewModel';
import { VwCodeTypeCheckListResponse } from '../../Model/Response/VwCodeTypeCheckListResponse';
import { VwCodeCheckListResponse } from '../../Model/Response/VwCodeCheckListResponse';

/*
  Generated class for the CodeCheckServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class CodeCheckServicesProvider {
  private CodeChecks: CodeCheckListResponse;
  private VwCodeTypeChecks: VwCodeTypeCheckListResponse;
  private VwCodeChecks: VwCodeCheckListResponse;
  private CodeCheck: CodeCheckResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "CodeCheck";
    console.log("Hello CodeCheckServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number,schemaname:string,tablename:string, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "CodeCheck?PageSize={1}&PageNumber={2}&keyword={3}&schemaname={4}&tablename={5}&order_type={6}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,schemaname,tablename,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.CodeChecks = new CodeCheckListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.CodeChecks;
    });
  }

  public GetListsViewCodeTypeCheckByAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "CodeCheck/ViewCodeTypeCheck?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.VwCodeTypeChecks = new VwCodeTypeCheckListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.VwCodeTypeChecks;
    });
  }
  public GetListsViewCodeCheckByAsync(PageSize: number, PageNumber: number, keyword: string,check_type:string,order_type:boolean) {
    var sub_url: string;
     sub_url = "CodeCheck/ViewCodeCheck?PageSize={1}&PageNumber={2}&keyword={3}&check_type={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,check_type,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.VwCodeChecks = new VwCodeCheckListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.VwCodeChecks;
    });
  }

  public PostPutAsync(CodeCheckViewModel:CodeCheckViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "CodeCheck";
    else
    sub_url = "CodeCheck?schemaname={1}&tablename={2}&columnname={3}";
    sub_url = String.Format(sub_url, this.ctl,CodeCheckViewModel.schemaname,CodeCheckViewModel.tablename,CodeCheckViewModel.columnname);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,CodeCheckViewModel,mode).map(
      item => {
        this.CodeCheck = new CodeCheckResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.CodeCheck;
      }
    );
  }

  public DeleteAsync(schemaname:string,tablename:string,columnname:string) {
    var sub_url: string;
    sub_url = "CodeCheck?schemaname={1}&tablename={2}&columnname={3}";
    sub_url = String.Format(sub_url, this.ctl,schemaname,tablename,columnname);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.CodeCheck = new CodeCheckResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.CodeCheck;
      }
    );
  }



  public GetSqlAsync(schemaname:string,tablename:string,columnname:string,method:string) {
    var sub_url: string;
    sub_url = "CodeCheck/get_{4}_sql?schemaname={1}&tablename={2}&columnname={3}";
    sub_url = String.Format(sub_url, this.ctl,schemaname,tablename,columnname,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
}



