import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { LineMessageListResponse } from "../../Model/Response/LineMessageListResponse";
import { LineMessageResponse } from "../../Model/Response/LineMessageResponse";
import { LineMessageViewModel } from '../../Model/ViewModel/LineMessageViewModel';

/*
  Generated class for the LineMessageServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class LineMessageServicesProvider {
  private LineMessages: LineMessageListResponse;
  private LineMessage: LineMessageResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "LineMessage";
    console.log("Hello LineMessageServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,tokenname:string,order_type:boolean) {
    var sub_url: string;
     sub_url = "LineMessage?PageSize={1}&PageNumber={2}&keyword={3}&tokenname={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,tokenname,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.LineMessages = new LineMessageListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.LineMessages;
    });
  }
  public GetLogListsByAsync(PageSize: number, PageNumber: number, keyword: string,subject_no:string,order_type:boolean) {
    var sub_url: string;
     sub_url = "LineMessage/Log?PageSize={1}&PageNumber={2}&keyword={3}&subject_no={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,subject_no,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.LineMessages = new LineMessageListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.LineMessages;
    });
  }



  public PostPutAsync(LineMessageViewModel:LineMessageViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "LineMessage";
    else
    sub_url = "LineMessage?subject_no={1}";
    sub_url = String.Format(sub_url, this.ctl,LineMessageViewModel.subject_no);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,LineMessageViewModel,mode).map(
      item => {
        this.LineMessage = new LineMessageResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.LineMessage;
      }
    );
  }

  public DeleteAsync(subject_no:string) {
    var sub_url: string;
    sub_url = "LineMessage?subject_no={1}";
    sub_url = String.Format(sub_url, this.ctl,subject_no);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.LineMessage = new LineMessageResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.LineMessage;
      }
    );
  }

}



