import { SingleModelResponse } from "./SingleModelResponse";
import { RolePermissionViewModel } from "../ViewModel/RolePermissionViewModel";

export class RolePermissionResponse extends SingleModelResponse<
  RolePermissionViewModel
> {
  Model: RolePermissionViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new RolePermissionViewModel();
      this.Model.RoleName = obj.Model.RoleName;
      this.Model.permission_no = obj.Model.permission_no;
      this.Model.creator = obj.Model.creator;
      this.Model.create_time = obj.Model.create_time;
      this.Model.modifier = obj.Model.modifier;
      this.Model.last_update_time = obj.Model.last_update_time;
    }
  }
}



