import { SingleModelResponse } from "./SingleModelResponse";
import { GlossaryViewModel } from "../ViewModel/GlossaryViewModel";

export class GlossaryResponse extends SingleModelResponse<GlossaryViewModel> {
  Model: GlossaryViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new GlossaryViewModel();
      this.Model.cht_name = obj.Model.cht_name;
      this.Model.eng_name = obj.Model.eng_name;
      this.Model.eng_name_length = obj.Model.eng_name_length;
      this.Model.data_type = obj.Model.data_type;
      this.Model.glossary_type = obj.Model.glossary_type;
      this.Model.creator = obj.Model.creator;
      this.Model.create_time = obj.Model.create_time;
      this.Model.modifier = obj.Model.modifier;
      this.Model.last_update_time = obj.Model.last_update_time;
      this.Model.glossary_type_desc=obj.Model.glossary_type==='T'?"資料表":"欄位";
    }
  }
}



