import { UserRoleViewModel } from './../../Model/ViewModel/UserRoleViewModel';
import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { UserRoleListResponse } from "../../Model/Response/UserRoleListResponse";
import { UserRoleResponse } from "../../Model/Response/UserRoleResponse";

/*
  Generated class for the UserRoleServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class UserRoleServicesProvider {
  private UserRoles: UserRoleListResponse;
  private UserRole: UserRoleResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "UserRole";
    console.log("Hello UserRoleServiceProvider Provider");
  }
  public GetUserListsAsync(PageSize: number, PageNumber: number,keyword:string, roleName: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "UserRole?PageSize={1}&PageNumber={2}&keyword={3}&roleName={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber,keyword, roleName,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.UserRoles = new UserRoleListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.UserRoles;
    });
  }
  public GetRoleListsAsync(PageSize: number, PageNumber: number,keyword:string, userName: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "UserRole?PageSize={1}&PageNumber={2}&keyword={3}&userName={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber,keyword, userName,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.UserRoles = new UserRoleListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.UserRoles;
    });
  }

  public PostAsync(UserRole:UserRoleViewModel) {
    var sub_url: string;
    sub_url = "UserRole";
    return this.Services.PostAsync(sub_url, this.ctl, true,UserRole).map(
      item => {
        this.UserRole = new UserRoleResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.UserRole;
      }
    );
  }
  public PostListAsync(UserRole:UserRoleViewModel[]) {
    var sub_url: string;
    sub_url = "UserRole/AddList";

    return this.Services.PostAsync(sub_url, this.ctl, true,UserRole).map(
      item => {
        this.UserRole = new UserRoleResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.UserRole;
      }
    );
  }
  public DeleteAsync(userName:string,roleName:string) {
    var sub_url: string;
    sub_url =String.Format("UserRole?userName={0}&roleName={1}",userName,roleName);
    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.UserRole = new UserRoleResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.UserRole;
      }
    );
  }




}



