

  /**
     * 回傳IQueryable物件內容及訊息
     *
     * @public
     * @class ListModelResponse
     * @implements  IListModelResponse
     * @param   {Function}    [name]
     */
    export class ListModelResponse<TModel> {
      /**
       * 回傳訊息
       *
       * @instance
       * @public
       * @memberof ListModelResponse
       * @function Message
       * @type string
       */
      Message: string;
      /**
       * 是否有異常
       *
       * @instance
       * @public
       * @memberof ListModelResponse
       * @function DidError
       * @type boolean
       */
      DidError: boolean;
      /**
       * 異常訊息內容
       *
       * @instance
       * @public
       * @memberof ListModelResponse
       * @function ErrorMessage
       * @type string
       */
      ErrorMessage: string;
      /**
       * 單一頁面回傳筆數(0代表回傳全部)
       *
       * @instance
       * @public
       * @memberof ListModelResponse
       * @function PageSize
       * @type number
       */
      PageSize: number;
      /**
       * 頁面編號以1為開始(0代表不分頁)
       *
       * @instance
       * @public
       * @memberof ListModelResponse
       * @function PageNumber
       * @type number
       */
      PageNumber: number;
      /**
       * 符合條件總筆數
       *
       * @instance
       * @public
       * @memberof ListModelResponse
       * @function TotalRows
       * @type number
       */
      TotalRows: number;
      /**
       * 目前頁面筆數
       *
       * @instance
       * @public
       * @memberof ListModelResponse
       * @function PageRows
       * @type number
       */
      PageRows: number;
      /**
       * 回傳結果集合
       * 符合條件總頁數
       *
       * @instance
       * @public
       * @readonly
       * @memberof ListModelResponse
       * @function TotalPages
       * @type number
       */
      TotalPages: number;
      /**
       * 資料模組
       *
       * @instance
       * @public
       * @memberof ListModelResponse
       * @function Model
       * @type System.Collections.Generic.IEnumerable
       */
      Model: TModel[];
      Token:string;
      ExpireDate:Date;
      constructor(obj?: any, )
      {
        this.Message=obj && obj.Message || null;
        this.DidError=obj && obj.DidError || null;
        this.ErrorMessage=obj && obj.ErrorMessage || null;
        this.PageNumber=obj && obj.PageNumber || null;
        this.PageRows=obj && obj.PageRows || null;
        this.TotalPages=obj && obj.TotalPages || null;
        this.Model=obj && obj.Model || null;


      }
  }

