import { ListModelResponse } from "./ListModelResponse";
import { SchViewModel } from "../ViewModel/SchViewModel";

/**
 * 回傳IQueryable物件內容及訊息
 *
 * @public
 * @class ListModelResponse
 * @implements  IListModelResponse
 * @param   {Function}    [name]
 */
export class SchListResponse extends ListModelResponse<SchViewModel> {
  Model: SchViewModel[];
  constructor(obj?: any) {
    super(obj);
    this.Model = [];
    this.Message = obj.Messag;
    this.DidError = obj.DidError;
    this.ErrorMessage = (obj && obj.ErrorMessage) || null;
    this.PageNumber = obj.PageNumber;
    this.PageSize = obj.PageSize;
    this.PageRows = obj.PageRows;
    this.TotalPages = obj.TotalPages;
    this.TotalRows = obj.TotalRows;

    if (obj.DidError === false) {
      obj.Model.forEach(data => {
        let item = new SchViewModel();
        item.sch_no=data.sch_no;
        item.sch_name=data.sch_name;
        item.job_cnt=data.job_cnt;
        item.sch_description=data.sch_description;
        this.Model.push(item);
      }) || null;
    }
  }

}



