import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { TableDetailExtListResponse } from "../../Model/Response/TableDetailExtListResponse";
import { TableDetailExtResponse } from "../../Model/Response/TableDetailExtResponse";
import { TableDetailExtViewModel } from '../../Model/ViewModel/TableDetailExtViewModel';


/*
  Generated class for the TableDetailExtServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class TableDetailExtServicesProvider {
  private TableDetailExts: TableDetailExtListResponse;
  private TableDetailExt: TableDetailExtResponse;
    ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "TableDetailExt";
    console.log("Hello TableDetailExtServiceProvider Provider");
  }
  public GetTableDetailByColumnListsAsync(
    PageSize: number,
    PageNumber: number,
    columnname:string,
    keyword: string,
    order_type:boolean
  ) {
    var sub_url: string;

    sub_url =
      "TableDetail/TableDetailExtList?PageSize={1}&PageNumber={2}&columnname={3}&keyword={4}&order_type={5}&is_filter=true";
    sub_url = String.Format(
      sub_url,
      this.ctl,
      PageSize,
      PageNumber,
      columnname,
      keyword,
      order_type,
    );
    return this.Services.GetAsync(sub_url, this.ctl, true).map(item => {
      this.TableDetailExts = new TableDetailExtListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.TableDetailExts;
    });
  }
  public GetTableDetailExtListsAsync(PageSize: number, PageNumber: number,schemaname:string,tablename:string, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "TableDetail/TableDetailExtList?PageSize={1}&PageNumber={2}&schemaname={3}&tablename={4}&keyword={5}&order_type={6}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber,schemaname,tablename, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.TableDetailExts = new TableDetailExtListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.TableDetailExts;
    });
  }


  public PostPutAsync(TableDetailExtViewModel:TableDetailExtViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "TableDetailExt";
    else
    sub_url = "TableDetailExt/{1}";
    sub_url = String.Format(sub_url, this.ctl,TableDetailExtViewModel.tablename);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,TableDetailExtViewModel,mode).map(
      item => {
        this.TableDetailExt = new TableDetailExtResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.TableDetailExt;
      }
    );
  }


}



