import {StringResponse} from '../../Model/Response/StringResponse';
import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FilePathListResponse } from "../../Model/Response/FilePathListResponse";
import { FilePathResponse } from "../../Model/Response/FilePathResponse";
import { FilePathViewModel } from '../../Model/ViewModel/FilePathViewModel';

/*
  Generated class for the FilePathServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FilePathServicesProvider {
  private FilePaths: FilePathListResponse;
  private FilePath: FilePathResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "FilePath";
    console.log("Hello FilePathServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "FilePath?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FilePaths = new FilePathListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FilePaths;
    });
  }
  public GetDefaultListsByAsync(func_key:string) {
    var sub_url: string;
     sub_url = "FilePath/DefaultPath?func_key={1}";
    sub_url = String.Format(sub_url, this.ctl,func_key);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FilePaths = new FilePathListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FilePaths;
    });
  }
  public GetPathAsync(file_path_type_key:string) {
    var sub_url: string;
     sub_url = "FilePath?exec_file_seq=1&file_path_type_key={0}";
     sub_url=String.Format(sub_url,file_path_type_key);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FilePath = new FilePathResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        Model: item.Model
      });
      return this.FilePath;
    });
  }

  public PostPutAsync(FilePathViewModel:FilePathViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FilePath";
    else
    sub_url = "FilePath?exec_file_seq={1}&file_path_type_key={2}";
    sub_url = String.Format(sub_url, this.ctl,FilePathViewModel.exec_file_seq,FilePathViewModel.file_path_type_key);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FilePathViewModel,mode).map(
      item => {
        this.FilePath = new FilePathResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.FilePath;
      }
    );
  }

  public DeleteAsync(exec_file_seq:number,file_path_type_key:string) {
    var sub_url: string;
    sub_url = "FilePath?exec_file_seq={1}&file_path_type_key={2}";
    sub_url = String.Format(sub_url, this.ctl,exec_file_seq,file_path_type_key);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.FilePath = new FilePathResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FilePath;
      }
    );
  }

  public GetSqlAsync(exec_file_seq:number,file_path_type_key:string,method:string) {
    var sub_url: string;
    sub_url = "FilePath/get_{3}_sql?exec_file_seq={1}&file_path_type_key={2}";
    sub_url = String.Format(sub_url, this.ctl,exec_file_seq,file_path_type_key,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
}



