import { Component, ElementRef } from "@angular/core";
import {
  LoadingController,
  AlertController,
  NavController,
  ModalController,
  Platform,
  ToastController
} from "ionic-angular";
import { AuthServicesProvider } from "../../providers/auth-services/auth-services";
import { UserMenuResponse } from "../../Model/Response/UserMenuResponse";

/**
 * Generated class for the GlobalComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */

@Component({
  selector: "global",
  templateUrl: "global.html"
})
export class GlobalComponent {
  text: string;
  loading: any;
  is_app: boolean;
  network: boolean = true;

  constructor(
    public loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private authServices: AuthServicesProvider,
    public modalCtrl: ModalController,
    private toastCtrl: ToastController,
    public platform: Platform
  ) {
    console.log("Hello GlobalComponent Component");
    // this.text = 'Hello World';
  }

  public Replace = function(
    originalString: string,
    oldValue: string,
    newValue: string,
    ignoreCase: boolean = false
  ) {
    //
    // if invalid data, return the original string
    //
    if (
      originalString == null ||
      oldValue == null ||
      newValue == null ||
      oldValue.length === 0
    )
      return originalString;
    //
    // do text replacement
    //
    var dest = "";
    var source: string = originalString;
    if (ignoreCase) {
      source = source.toLocaleLowerCase();
      oldValue = oldValue.toLowerCase();
    }
    //
    // find first match
    //
    var StartPos = 0;
    var EndPos = source.indexOf(oldValue, StartPos);
    var Skip = EndPos >= 0 ? EndPos - StartPos : source.length - StartPos;
    //
    // while we found a matched string
    //
    while (EndPos > -1) {
      //
      // copy original string skipped segment
      //
      if (Skip > 0) dest += originalString.substr(StartPos, Skip);
      //
      // copy new value
      //
      dest += newValue;
      //
      // skip over old value
      //
      StartPos = EndPos + oldValue.length;
      //
      // find next match
      //
      EndPos = source.indexOf(oldValue, StartPos);
      Skip = EndPos >= 0 ? EndPos - StartPos : source.length - StartPos;
    }
    //
    // append the last skipped string segment from original string
    //
    if (Skip > 0) dest += originalString.substr(StartPos, Skip);

    return dest;
  };
  adjust(element: ElementRef): void {
    let textArea = element.nativeElement.getElementsByTagName("textarea")[0];
    textArea.style.overflow = "hidden";
    textArea.style.height = "auto";
    textArea.style.height = textArea.scrollHeight + "px";
  }
  public check_app(): boolean {
    return (
      document.URL.indexOf("http://") === -1 &&
      document.URL.indexOf("https://") === -1
    );
  }
  public check_auth(navCtrl: NavController): boolean {
    if (this.authServices.authenticated() === false) {
      navCtrl.setRoot("LoginPage");
      return false;
    } else {
      return true;
    }
  }
  public getUserInfo(): UserMenuResponse {
    return this.authServices.getUserInfo();
  }
  public logout() {
    this.authServices.logout();
  }
  public timeout() {
    this.authServices.timeout();
  }

  public createLoader(message: string = "資料載入中...") {
    // Optional Parameter
    if (!this.loading) {
      this.loading = this.loadingCtrl.create({
        content: message
      });
    }
  }
  public dismissLoading() {
    if (this.loading) {
      this.loading.dismiss();
      this.loading = null;
    }
  }
  copyTextToClipboard(Text: string) {
    var textArea = document.createElement("textarea");

    textArea.style.position = "fixed";
    textArea.style.top = "0";
    textArea.style.left = "0";
    textArea.style.width = "2em";
    textArea.style.height = "2em";
    textArea.style.padding = "0";
    textArea.style.border = "none";
    textArea.style.outline = "none";
    textArea.style.boxShadow = "none";
    textArea.style.background = "transparent";
    textArea.value = Text;

    document.body.appendChild(textArea);
    textArea.focus();
    textArea.setSelectionRange(0, textArea.value.length);
    // textArea.select();
    try {
      var successful = document.execCommand("copy");
      var msg = successful ? "successful" : "unsuccessful";
      console.log("Copying text command was " + msg);
      let toast = this.toastCtrl.create({
        message: "已經複製到剪貼簿",
        duration: 1000,
        position: "middle"
      });
      toast.present();
    } catch (err) {
      console.log("Oops, unable to copy");
    }
    document.body.removeChild(textArea);
  }

  public showError(text) {
    this.dismissLoading();
    let modal = this.modalCtrl.create("ShowMessagePage", {
      message: text,
      title: "異常",
      error: true
    });
    modal.present();
  }
  public showMessage(title, text) {
    this.dismissLoading();
    let modal = this.modalCtrl.create("ShowMessagePage", {
      message: text,
      title: title,
      error: false
    });
    modal.present();
  }
  showPopup(title, text) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: text.substring(0, 512),
      buttons: [
        {
          text: "確定"
        }
      ]
    });
    alert.present();
  }
}



