import {StringResponse} from '../../Model/Response/StringResponse';
import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { GlossaryListResponse } from "../../Model/Response/GlossaryListResponse";
import { GlossaryResponse } from "../../Model/Response/GlossaryResponse";
import { GlossaryViewModel } from '../../Model/ViewModel/GlossaryViewModel';

/*
  Generated class for the GlossaryServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class GlossaryServicesProvider {
  private Glossarys: GlossaryListResponse;
  private Glossary: GlossaryResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "Glossary";
    console.log("Hello GlossaryServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "Glossary?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Glossarys = new GlossaryListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Glossarys;
    });
  }



  public PostPutAsync(GlossaryViewModel:GlossaryViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "Glossary";
    else
    sub_url = "Glossary/{1}";
    sub_url = String.Format(sub_url, this.ctl,GlossaryViewModel.eng_name);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,GlossaryViewModel,mode).map(
      item => {
        this.Glossary = new GlossaryResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.Glossary;
      }
    );
  }



  public DeleteAsync(eng_name:string) {
    var sub_url: string;
    sub_url = "Glossary/{1}";
    sub_url = String.Format(sub_url, this.ctl,eng_name);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.Glossary = new GlossaryResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.Glossary;
      }
    );
  }


  public GetSqlAsync(eng_name:string,method:string) {
    var sub_url: string;
    sub_url = "Glossary/get_{2}_sql/{1}";
    sub_url = String.Format(sub_url, this.ctl,eng_name,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }

}



