import { LineMessageViewModel } from "../ViewModel/LineMessageViewModel";
import { ListModelResponse } from "./ListModelResponse";

export class LineMessageListResponse extends ListModelResponse<
  LineMessageViewModel
> {
  Model: LineMessageViewModel[];
  constructor(obj?: any) {
    super(obj);
    this.Model = [];
    this.Message = obj.Messag;
    this.DidError = obj.DidError;
    this.ErrorMessage = (obj && obj.ErrorMessage) || null;
    this.PageNumber = obj.PageNumber;
    this.PageSize = obj.PageSize;
    this.PageRows = obj.PageRows;
    this.TotalPages = obj.TotalPages;
    this.TotalRows = obj.TotalRows;
    if (this.DidError === false) {
      obj.Model.forEach(data => {
        let item = new LineMessageViewModel();
        item.tokenname = data.tokenname;
        item.subject_no = data.subject_no;
        item.ssis_job_no = data.subject_no;
        item.subject = data.subject;
        item.message = data.message;
        if (data.message) item.short_message = data.message.substr(0, 50);
        else item.short_message = "";
        item.res_message = data.res_message || null;
        if (data.res_message)
          item.short_res_message = data.res_message.substr(0, 50);
        else item.short_res_message = "";
        item.sch_cnt = data.sch_cnt;
        item.is_send_now = data.is_send_now;
        item.create_time = data.create_time;
        if (item.sch_cnt > 0) item.sch_color = "danger";
        else item.sch_color = "light";
        this.Model.push(item);
      }) || null;
    }
  }
}



