/**
 * 作業ViewModel
 *
 * @public
 * @class jobViewModel
 */
export class JobViewModel {
  /**
   * SSIS作業編號
   *
   * @instance
   * @public
   * @memberof jobViewModel
   * @function ssis_job_no
   * @type string
   */
  ssis_job_no: string;
  /**
   * SSIS作業名稱
   *
   * @instance
   * @public
   * @memberof jobViewModel
   * @function ssis_job_name
   * @type string
   */
  ssis_job_name: string;
  /**
   * 顯示名稱
   *
   * @instance
   * @public
   * @readonly
   * @memberof jobViewModel
   * @function disp_name
   * @type string
   */
  disp_name: string;
  /**
   * 最後執行狀態主鍵(參考012共用代碼)
   *
   * @instance
   * @public
   * @memberof jobViewModel
   * @function last_exec_status_key
   * @type string
   */
  last_exec_status_key: string;
  /**
   * 最後執行狀態
   *
   * @instance
   * @public
   * @memberof jobViewModel
   * @function last_exec_status
   * @type string
   */
  last_exec_status: string;
  /**
   * 顯示名稱
   *
   * @instance
   * @public
   * @readonly
   * @memberof jobViewModel
   * @function last_exec_status_desc
   * @type string
   */
  last_exec_status_desc: string;
  /**
   * 顯示顏色
   *
   * @instance
   * @public
   * @readonly
   * @memberof jobViewModel
   * @function last_exec_status_color
   * @type string
   */
  last_exec_status_color: string;
  /**
   * 最後執行時間
   *
   * @instance
   * @public
   * @memberof jobViewModel
   * @function last_exec_date
   * @type ?System.DateTime
   */
  last_exec_date: Date;
  /**
   * 啟用註記
   *
   * @instance
   * @public
   * @memberof jobViewModel
   * @function is_active
   * @type boolean
   */
  is_active: boolean;
  /**
   * 啟用說明
   *
   * @instance
   * @public
   * @readonly
   * @memberof jobViewModel
   * @function is_active_desc
   * @type string
   */
  is_active_desc: string;
  /**
   * 啟用顏色
   *
   * @instance
   * @public
   * @readonly
   * @memberof jobViewModel
   * @function is_active_color
   * @type string
   */
  is_active_color: string;
  /**
   * 排程數
   *
   * @instance
   * @public
   * @memberof jobViewModel
   * @function sch_cnt
   * @type number
   */
  sch_cnt: number;
  /**
   * 最後結束時間
   *
   * @instance
   * @public
   * @readonly
   * @memberof jobViewModel
   * @function end_time
   * @type string
   */
  end_time: string;
  /**
   * 執行花費時間
   *
   * @instance
   * @public
   * @readonly
   * @memberof jobViewModel
   * @function duration
   * @type string
   */
  duration: string;
  /**
   * 建立者
   *
   * @instance
   * @public
   * @memberof jobViewModel
   * @function creator
   * @type string
   */
  creator: string;
  /**
   * 建立時間
   *
   * @instance
   * @public
   * @memberof jobViewModel
   * @function create_time
   * @type System.DateTime
   */
  create_time: Date;
  /**
   * 最後更新者
   *
   * @instance
   * @public
   * @memberof jobViewModel
   * @function modifier
   * @type string
   */
  modifier: string;
  /**
   * 最後更新時間
   *
   * @instance
   * @public
   * @memberof jobViewModel
   * @function last_update_time
   * @type System.DateTime
   */
  last_update_time: Date;

  sch_color: string;
  /// <summary>
  ///  電子郵件次數
  /// <summary>
  email_cnt: number;
  /// <summary>
  ///  電子郵件顏色
  /// <summary>
  email_color: string;
  /// <summary>
  ///  作業迴圈筆數
  /// <summary>
  loop_cnt: number;
  /// <summary>
  ///  作業迴圈顏色
  /// <summary>
  loop_color: string;
  /// <summary>
  ///  重試次數
  /// <summary>
  public retry_cnt: number;

  /// <summary>
  ///  延遲分鐘數
  /// <summary>
  public delay_min: number;

  /// <summary>
  ///  事件設定筆數
  /// <summary>
  event_cnt: number;
  /// <summary>
  ///  事件設定顏色
  /// <summary>
  event_color: string;
  /// <summary>
  ///  執行後自訂sql筆數
  /// <summary>
  processed_sql_cnt:number;
  /// <summary>
  ///  執行後自訂sql顏色
  /// <summary>
  processed_sql_color:string;
}



