export class ProgramViewModel {
  /// <summary>
  ///  程式編號
  /// <summary>
  public program_no: string;
  /// <summary>
  ///  程式說明
  /// <summary>
  public program_desc: string;
  /// <summary>
  ///  程式名稱
  /// <summary>
  public program_name: string;
  /// <summary>
  ///  業務別主鍵
  /// <summary>
  public bus_type_key: string;
  /// <summary>
  ///  程式類型主鍵
  /// <summary>
  public program_type_key: string;
  /// <summary>
  ///  程式類型序號
  /// <summary>
  public program_type_seq: number;
  /// <summary>
  ///  程式路徑主鍵
  /// <summary>
  public program_path_key: string;
  /// <summary>
  ///  主程式編號
  /// <summary>
  public master_program_no: string;
  /// <summary>
  ///  父程式編號
  /// <summary>
  public parent_program_no: string;
  /// <summary>
  ///  選單主鍵
  /// <summary>
  public menu_key: string;
  /// <summary>
  ///  稽核註記
  /// <summary>
  public audit_flag: boolean;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;

  /// <summary>
  ///  sql語法
  /// </summary>
  public sql_statement: string;

  public is_active: boolean;
  /**
   * 啟用說明
   *
   * @instance
   * @public
   * @readonly
   * @memberof jobViewModel
   * @function is_active_desc
   * @type string
   */
  public is_active_desc: string;
  /**
   * 啟用顏色
   *
   * @instance
   * @public
   * @readonly
   * @memberof jobViewModel
   * @function is_active_color
   * @type string
   */
  public is_active_color: string;

  /// <summary>
  ///  業務別
  /// <summary>
  public bus_type: string;

  public checked: boolean;
}



