import { RolePermissionViewModel } from './../../Model/ViewModel/RolePermissionViewModel';
import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { RolePermissionListResponse } from "../../Model/Response/RolePermissionListResponse";
import { RolePermissionResponse } from "../../Model/Response/RolePermissionResponse";

/*
  Generated class for the RolePermissionServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class RolePermissionServicesProvider {
  private RolePermissions: RolePermissionListResponse;
  private RolePermission: RolePermissionResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "RolePermission";
    console.log("Hello RolePermissionServiceProvider Provider");
  }
  public GetPermissionListsAsync(PageSize: number, PageNumber: number,keyword:string, RoleName: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "RolePermission?PageSize={1}&PageNumber={2}&keyword={3}&RoleName={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber,keyword, RoleName,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.RolePermissions = new RolePermissionListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.RolePermissions;
    });
  }
  public GetRoleListsAsync(PageSize: number, PageNumber: number,keyword:string, permission_no: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "RolePermission?PageSize={1}&PageNumber={2}&keyword={3}&permission_no={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber,keyword, permission_no,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.RolePermissions = new RolePermissionListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.RolePermissions;
    });
  }

  public PostAsync(RolePermission:RolePermissionViewModel) {
    var sub_url: string;
    sub_url = "RolePermission";
    console.log(RolePermission);
    return this.Services.PostAsync(sub_url, this.ctl, true,RolePermission).map(
      item => {
        this.RolePermission = new RolePermissionResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.RolePermission;
      }
    );
  }
  public PutAsync(RoleName:string,permission_no:String,RolePermission:RolePermissionViewModel) {
    var sub_url: string;
    sub_url =String.Format("RolePermission?RoleName={0}&permission_no={1}",RoleName,permission_no);
    return this.Services.PutAsync(sub_url, this.ctl, true,RolePermission).map(
      item => {
        this.RolePermission = new RolePermissionResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.RolePermission;
      }
    );
  }
  public PostListAsync(RolePermission:RolePermissionViewModel[]) {
    var sub_url: string;
    sub_url = "RolePermission/AddList";

    return this.Services.PostAsync(sub_url, this.ctl, true,RolePermission).map(
      item => {
        this.RolePermission = new RolePermissionResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.RolePermission;
      }
    );
  }
  public DeleteAsync(RoleName:string,permission_no:String) {
    var sub_url: string;
    sub_url =String.Format("RolePermission?RoleName={0}&permission_no={1}",RoleName,permission_no);
    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.RolePermission = new RolePermissionResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.RolePermission;
      }
    );
  }




}



