import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { DirectoryBrowserListResponse } from "../../Model/Response/DirectoryBrowserListResponse";
import { DirectoryBrowserResponse } from "../../Model/Response/DirectoryBrowserResponse";
import { FtpDirectoryBrowserViewModel } from '../../Model/ViewModel/FtpDirectoryBrowserViewModel';
import { StringResponse } from "../../Model/Response/StringResponse";

/*
  Generated class for the DirectoryBrowserServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FtpDirectoryBrowserServicesProvider {
  private DirectoryBrowsers: DirectoryBrowserListResponse;
  private DirectoryBrowser: DirectoryBrowserResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "FtpDirectoryBrowser";
    console.log("Hello FtpDirectoryBrowserServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,path:string,order_type:boolean,remote_conn_id:string, conn_type_key:string,encrypt_method_key:string) {
    var sub_url: string;
     sub_url = "FtpDirectoryBrowser?PageSize={1}&PageNumber={2}&keyword={3}&path={4}&order_type={5}&remote_conn_id={6}&conn_type_key={7}&encrypt_method_key={8}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,path,order_type,remote_conn_id,conn_type_key,encrypt_method_key);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.DirectoryBrowsers = new DirectoryBrowserListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.DirectoryBrowsers;
    });
  }



  public PostPutAsync(path:string,FtpDirectoryBrowserViewModel:FtpDirectoryBrowserViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FtpDirectoryBrowser";
    else
    sub_url = "FtpDirectoryBrowser?path={1}";
    sub_url = String.Format(sub_url, this.ctl,path);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FtpDirectoryBrowserViewModel,mode).map(
      item => {
        this.DirectoryBrowser = new DirectoryBrowserResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.DirectoryBrowser;
      }
    );
  }

  public DeleteAsync(path:string,remote_conn_id:string, conn_type_key:string,encrypt_method_key:string) {
    var sub_url: string;
    sub_url = "FtpDirectoryBrowser?path={1}&remote_conn_id={2}&conn_type_key={3}&encrypt_method_key={4}";
    sub_url = String.Format(sub_url, this.ctl,path,remote_conn_id,conn_type_key,encrypt_method_key);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.DirectoryBrowser = new DirectoryBrowserResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.DirectoryBrowser;
      }
    );
  }

  public HasFile(path:string,remote_conn_id:string, conn_type_key:string,encrypt_method_key:string) {
    var sub_url: string;
    sub_url = "FtpDirectoryBrowser/HasFile?path={1}&remote_conn_id={2}&conn_type_key={3}&encrypt_method_key={4}";
    sub_url = String.Format(sub_url, this.ctl,path,remote_conn_id,conn_type_key,encrypt_method_key);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
       return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

      }
    );
  }
}



