import { SingleModelResponse } from "./SingleModelResponse";
import { JobLoopEventViewModel } from "../ViewModel/JobLoopEventViewModel";

export class JobLoopEventResponse extends SingleModelResponse<
  JobLoopEventViewModel
> {
  Model: JobLoopEventViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      if (obj.Model.loop_seq) {
        this.Model = new JobLoopEventViewModel();
        this.Model.loop_seq = obj.Model.loop_seq; //迴圈序號
        this.Model.ssis_job_no = obj.Model.ssis_job_no; //SSIS作業編號
        this.Model.job01 = (obj && obj.Model.job01) || null; //作業迴圈欄位一
        this.Model.job02 = (obj && obj.Model.job02) || null; //作業迴圈欄位二
        this.Model.job03 = (obj && obj.Model.job03) || null; //作業迴圈欄位三
        this.Model.event_type_key = obj.Model.event_type_key; //觸發事件類別主鍵
        this.Model.src_path_key = obj.Model.src_path_key; //來源路徑主鍵
        this.Model.file_keyword = (obj && obj.Model.file_keyword) || null; //檔案搜尋關鍵字
        this.Model.remote_conn_id = (obj && obj.Model.remote_conn_id) || null; //遠端連線編號
        this.Model.period_min = obj.Model.period_min; //每隔分鐘數
        this.Model.active_start_date = obj.Model.active_start_date; //開始作業日期
        this.Model.active_start_time = obj.Model.active_start_time; //開始作業時間
        this.Model.next_start_time = obj.Model.next_start_time; //下次開始執行時間
        this.Model.sbp_conn_id = obj.Model.sbp_conn_id; //sbpclient連線編號
        this.Model.is_active = obj.Model.is_active; //啟用註記
        this.Model.is_active_desc = obj.Model.is_active_desc; //啟用註記
        this.Model.creator = obj.Model.creator; //鍵檔人員
        this.Model.create_time = obj.Model.create_time; //建立時間
        this.Model.modifier = obj.Model.modifier; //異動人員
        this.Model.last_update_time = obj.Model.last_update_time; //最後更新時間
      }
    }
  }
}



