  /**
     * 回傳IQueryable物件內容及訊息
     *
     * @public
     * @class ListModelResponse
     * @implements  IListModelResponse
     * @param   {Function}    [name]
     */
    export class SingleModelResponse<TModel> {
      /**
       * 回傳訊息
       *
       * @instance
       * @public
       * @memberof ListModelResponse
       * @function Message
       * @type string
       */
      Message: string;
      /**
       * 是否有異常
       *
       * @instance
       * @public
       * @memberof ListModelResponse
       * @function DidError
       * @type boolean
       */
      DidError: boolean;
      /**
       * 異常訊息內容
       *
       * @instance
       * @public
       * @memberof ListModelResponse
       * @function ErrorMessage
       * @type string
       */
      ErrorMessage: string;
      /**
       * 單一頁面回傳筆數(0代表回傳全部)
       *
       * @instance
       * @public
       * @memberof ListModelResponse
       * @function PageSize
       * @type number
       */

      Model: TModel;
      Token: string;
      ExpireDate:Date;
      constructor(obj?: any)
      {
        this.Message=obj && obj.Message || null;
        this.DidError=obj && obj.DidError || null;
        this.ErrorMessage=obj && obj.ErrorMessage || null;
        this.Model=obj && obj.Model || null;
      }
  }





