import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { SqlListResponse } from "../../Model/Response/SqlListResponse";
import { SqlResponse } from "../../Model/Response/SqlResponse";
import { StringResponse } from "../../Model/Response/StringResponse";
import { SqlViewModel } from '../../Model/ViewModel/SqlViewModel';
import { StringViewModel } from "../../Model/ViewModel/StringViewModel";
import { TableSetServicesProvider } from '../table-set-services/table-set-services';
/*
  Generated class for the SqlServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class SqlServicesProvider  extends TableSetServicesProvider {
  private Sqls: SqlListResponse;
  private Sql: SqlResponse;
  private Str: StringResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "Sql";
    console.log("Hello SqlServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "Sql?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Sqls = new SqlListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Sqls;
    });
  }



  public PostPutAsync(SqlViewModel:SqlViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "Sql";
    else
    sub_url = "Sql/{1}";
    sub_url = String.Format(sub_url, this.ctl,SqlViewModel.exec_table_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,SqlViewModel,mode).map(
      item => {
        this.Sql = new SqlResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.Sql;
      }
    );
  }

  public ExecuteAsync(StringViewModel:StringViewModel) {
    var sub_url: string;
    sub_url = "Sql/Execute";
    return this.Services.PostAsync(sub_url, this.ctl, true,StringViewModel).map(
      item => {
        this.Str = new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.Str;
      }
    );
  }
public FormatAsync(StringViewModel:StringViewModel) {
    var sub_url: string;
    sub_url = "Sql/Format";
    return this.Services.PostAsync(sub_url, this.ctl, true,StringViewModel).map(
      item => {
        this.Str = new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.Str;
      }
    );
  }

  public DeleteAsync(exec_table_seq:string) {
    var sub_url: string;
    sub_url = "Sql/{1}";
    sub_url = String.Format(sub_url, this.ctl,exec_table_seq);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.Sql = new SqlResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.Sql;
      }
    );
  }





}



