import { JobLoopEventListResponse } from "./../../Model/Response/JobLoopEventListResponse";
import { String } from "./../../Model/String";
import { Injectable } from "@angular/core";
import { JobLoopEventViewModel } from "../../Model/ViewModel/JobLoopEventViewModel";
import { JobLoopEventResponse } from "../../Model/Response/JobLoopEventResponse";
import { ServicesProvider } from "../services/services";
import { StringResponse } from "../../Model/Response/StringResponse";

/*
  Generated class for the JobLoopEventServicesProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class JobLoopEventServicesProvider {
  ctl: string;
  JobLoopEvents: JobLoopEventListResponse;
  JobLoopEvent: JobLoopEventResponse;
  constructor(
    private Services: ServicesProvider
  ) {
    // this.originUrl =apiUrl;
    this.ctl = "JobLoopEvent";

    console.log("Hello JobLoopEventServicesProvider Provider");
  }

  GetListsAsync(ssis_job_no: string,job01 :string,job02:string,job03:string,PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
    sub_url = "JobLoopEvent?PageSize={1}&PageNumber={2}&keyword={3}&ssis_job_no={4}&job01={5}&job02={6}&job03={7}&order_type={8}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword, ssis_job_no,job01,job02,job03,order_type);
   return this.Services.GetAsync(sub_url,this.ctl,true)
      .map(item => {
        this.JobLoopEvents = new JobLoopEventListResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          PageSize: item.PageSize,
          PageNumber: item.PageNumber,
          TotalRows: item.TotalRows,
          PageRows: item.PageRows,
          TotalPages: item.TotalPages,
          Model: item.Model
        });
        return this.JobLoopEvents;
      });
  }



  public PostPutAsync(JobLoopEventViewModel:JobLoopEventViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "JobLoopEvent";
    else
      sub_url = "JobLoopEvent/" + JobLoopEventViewModel.loop_seq;
    return this.Services.PostPutAsync(sub_url, this.ctl, true,JobLoopEventViewModel,mode).map(
      item => {
        this.JobLoopEvent = new JobLoopEventResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.JobLoopEvent;
      }
    );
  }

  public DeleteAsync(loop_seq:number) {
    var sub_url: string;
    sub_url = "JobLoopEvent/"+loop_seq;
    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.JobLoopEvent = new JobLoopEventResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.JobLoopEvent;
      }
    );
  }
  public GetSqlAsync(loop_seq:number,method:string) {
    var sub_url: string;
    sub_url = "JobLoopEvent/get_{2}_sql/{1}";
    sub_url = String.Format(sub_url, this.ctl,loop_seq,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
}



