import { FileBrowserViewModel } from "../ViewModel/FileBrowserViewModel";
import { ListModelResponse } from "./ListModelResponse";

export class FileBrowserListResponse extends ListModelResponse<
  FileBrowserViewModel
> {
  Model: FileBrowserViewModel[];
  constructor(obj?: any) {
    super(obj);
    this.Model = [];
    this.Message = obj.Messag;
    this.DidError = obj.DidError;
    this.ErrorMessage = (obj && obj.ErrorMessage) || null;
    this.PageNumber = obj.PageNumber;
    this.PageSize = obj.PageSize;
    this.PageRows = obj.PageRows;
    this.TotalPages = obj.TotalPages;
    this.TotalRows = obj.TotalRows;
    if (this.DidError === false) {
      if (obj.Model != null) {
        obj.Model.forEach(data => {
          let item = new FileBrowserViewModel();
          item.filename = data.filename;
          item.rename_filename = data.rename_filename;
          item.ext = data.ext;
          item.file_size = data.file_size;
          item.file_size_desc=data.file_size_desc;
          item.filefullname = data.filefullname;
          item.filepath = data.filepath;

          item.create_time = data.create_time;

          this.Model.push(item);
        }) || null;
      }
    }
  }
}



