import { SingleModelResponse } from "./SingleModelResponse";
import { TableViewModel } from "../ViewModel/TableViewModel";

export class TableResponse extends SingleModelResponse<TableViewModel> {
  Model: TableViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new TableViewModel();
      this.Model.schemaname=obj.Model.schemaname;
      this.Model.tablename=obj.Model.tablename;
      this.Model.file_group=obj.Model.file_group;
      this.Model.tablefullname=obj.Model.schemaname+'.' +obj.Model.tablename;
      this.Model.tablecname=obj && obj.Model.tablecname || null;
      this.Model.table_cat_key=obj.Model.table_cat_key;
      this.Model.table_cat_desc=obj.Model.table_cat_desc;
      this.Model.creator=obj.Model.creator;
      this.Model.create_time=obj.Model.create_time;
      this.Model.modifier=obj.Model.modifier;
      this.Model.last_update_time=obj.Model.last_update_time;
      this.Model.err_columnname=obj.Model.err_columnnamne|| null;
      this.Model.code_check_cnt=obj.Model.code_check_cnt;
      if (this.Model.code_check_cnt > 0) this.Model.code_check_color = "danger-left";
      else this.Model.code_check_color = "light";


    }
  }
}



