import { ListModelResponse } from "./ListModelResponse";
import { UserViewModel } from "../ViewModel/UserViewModel";

/**
 * 回傳IQueryable物件內容及訊息
 *
 * @public
 * @class ListModelResponse
 * @implements  IListModelResponse
 * @param   {Function}    [name]
 */
export class UserListResponse extends ListModelResponse<UserViewModel> {
  Model: UserViewModel[];
  constructor(obj?: any) {
    super(obj);
    this.Model = [];
    this.Message = obj.Messag;
    this.DidError = obj.DidError;
    this.ErrorMessage = (obj && obj.ErrorMessage) || null;
    this.PageNumber = obj.PageNumber;
    this.PageSize = obj.PageSize;
    this.PageRows = obj.PageRows;
    this.TotalPages = obj.TotalPages;
    this.TotalRows = obj.TotalRows;

    if (obj.DidError === false) {
      obj.Model.forEach(data => {
        let item = new UserViewModel();
        item.userName = data.userName;
        item.roleName=[];
        // item.PermissionViewModel=[];

          if(data.roleName!=null)
          {

            data.roleName.forEach(element => {
              item.roleName.push(element);
            });

          }
          // if(data.permission_no!=null)
          // {
          //   data.permission_no.forEach(element => {
          //     item.permission_no.push(element);
          //   });
          // }
        item.Email = data.Email;
        item.password = data.password;
        this.Model.push(item);
      }) || null;
    }
  }
}



