import { ApiUrlViewModel } from "../ViewModel/ApiUrlViewModel";
import { SingleModelResponse } from "./SingleModelResponse";

export class ApiUrlResponse extends SingleModelResponse<ApiUrlViewModel> {
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      if (obj.Model != null) {
        this.Model = new ApiUrlViewModel();
        this.Model.id = obj.Model.id;
        this.Model.url = obj.Model.url;
        this.Model.disp_name = obj.Model.disp_name;
        this.Model.is_default = obj.Model.codeis_default_desc;
        this.Model.is_url_forward = obj.Model.is_url_forward;
        if(obj.Model.is_url_forward===true)
        this.Model.url_forward="轉址";
          else
          this.Model.url_forward="";
      }
    }
  }
}



