export class LineMessageViewModel {
  /// <summary>
  ///  Token編號
  /// <summary>
  public tokenname: string;

  /// <summary>
  ///  標題編號
  /// <summary>
  public subject_no: string;
  /// <summary>
  ///  標題
  /// <summary>
  public subject: string;
  /// <summary>
  ///  訊息
  /// <summary>
  public message: string;

   /// <summary>
  ///  回應訊息
  /// <summary>
  public res_message: string;
  /// <summary>
  ///  排程數
  /// <summary>
  public sch_cnt: number;
  /// <summary>
  ///  排程顏色
  /// <summary>
  public sch_color: string;

  /// <summary>
  ///  作業編號
  /// <summary>
  public ssis_job_no: string;

  /// <summary>
  ///  是否立刻發送
  /// <summary>
  public is_send_now: boolean;

  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time:Date;

  /// <summary>
  ///  訊息
  /// <summary>
  public short_message: string;

   /// <summary>
  ///  回應訊息
  /// <summary>
  public short_res_message: string;
}



