import { ConnViewModel } from "./ConnViewModel";
import { String } from "../../Model/String";

export class ConnSmtpViewModel extends ConnViewModel {
  /// <summary>
  ///  伺服器名稱
  /// </summary>

  public server_name: string;

  /// <summary>
  ///  使用者名稱
  /// </summary>
  public user_name: string;

  /// <summary>
  ///  port
  /// </summary>
  public port: number;

  /// <summary>
  ///  ssl
  /// </summary>
  public ssl: boolean;
 /// <summary>
  ///  寄發者顯示名稱
  /// </summary>
  public display_name: string;


  public get_conn_string(): string {
    let conn_string: string =
      "ServerName={0};ServerUserName={1};ServerPort={2}";
    conn_string = String.Format(
      conn_string,
      this.server_name,
      this.user_name,
      this.port
    );
    if (this.ssl === true) conn_string += ";EnableSsl=true";
    if (this.display_name>'') conn_string += ";DisplayName="+ this.display_name ;

    return conn_string;
  }
}



