import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { RunJobResponse } from "../../Model/Response/RunJobResponse";
import { String } from "./../../Model/String";
/*
  Generated class for the RunJobServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class RunJobServicesProvider {
  ctl: string;
  res: RunJobResponse;
  constructor(
    private Services: ServicesProvider
  ) {
    console.log("Hello RunJobServicesProvider Provider");
    this.ctl = "RunJob";

  }
  RunJobAsync(ssis_job_no:string) {
    var sub_url: string;
    sub_url = "RunJob/Get/{1}";
    sub_url = String.Format(sub_url, this.ctl,ssis_job_no);
    return this.Services.GetAsync(sub_url,this.ctl,true)
      .map(item => {
          console.log("item.DidError="+item.DidError);
          this.res = new RunJobResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model,
        });
        return this.res;
      });
  }
  RunJobLoopAsync(ssis_job_no:string,job01:string,job02:string,job03:string) {
    var sub_url: string;
    sub_url = "RunJob/GetByJobParm/{1}?job01={2}&job02={3}&job03={4}";
    sub_url = String.Format(sub_url, this.ctl,ssis_job_no,job01,job02,job03);
    return this.Services.GetAsync(sub_url,this.ctl,true)
      .map(item => {
          console.log("item.DidError="+item.DidError);
          this.res = new RunJobResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model,
        });
        return this.res;
      });
  }

  RunExecGroupAsync(id:number,exec_group:string) {
    var sub_url: string;
    sub_url = "RunJob/GetByExecGroupParm/{1}?exec_group={2}";
    sub_url = String.Format(sub_url, this.ctl,id,exec_group);
    return this.Services.GetAsync(sub_url,this.ctl,true)
      .map(item => {
          console.log("item.DidError="+item.DidError);
          this.res = new RunJobResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model,
        });
        return this.res;
      });
  }

}



