import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { TableExtListResponse } from "../../Model/Response/TableExtListResponse";
import { TableExtResponse } from "../../Model/Response/TableExtResponse";
