import { SingleModelResponse } from "./SingleModelResponse";
import { DirectoryBrowserViewModel } from "../ViewModel/DirectoryBrowserViewModel";

export class DirectoryBrowserResponse extends SingleModelResponse<
  DirectoryBrowserViewModel
> {
  Model: DirectoryBrowserViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      if (obj.Model != null) {
        this.Model = new DirectoryBrowserViewModel();
        this.Model.directory_name = obj.Model.directory_name;
        this.Model.filepath = obj.Model.filepath;
        this.Model.create_time = obj.Model.create_time;
        this.Model.file_cnt = obj.Model.file_cnt;
        this.Model.sub_directory_cnt = obj.Model.sub_directory_cnt;
        if (this.Model.file_cnt > 0) this.Model.has_file = true;
        else this.Model.has_file = false;
        switch (this.Model.has_file) {
          case true: {
            this.Model.has_file_color = "primary";
            break;
          }

          default:
            this.Model.has_file_color = "primary";
        }
      }
    }
  }
}



