import { PermissionViewModel } from "./PermissionViewModel";

export class UserViewModel  {
  /**
   * 登入帳號或使用者名稱
   *
   * @instance
   * @public
   * @memberof Demo.userViewModel
   * @function userName
   * @type string
   */
  userName: string;
  /**
   * 登入密碼
   *
   * @instance
   * @public
   * @memberof Demo.userViewModel
   * @function password
   * @type string
   */
  password: string;
  /**
   * 角色
   *
   * @instance
   * @public
   * @memberof Demo.userViewModel
   * @function roleName
   * @type System.Collections.Generic.List$1
   */
  roleName: string[];
  /**
   * 電子郵件
   *
   * @instance
   * @public
   * @memberof Demo.userViewModel
   * @function Email
   * @type string
   */
  Email: string;
  token:string;
  ///權限編號清單
  PermissionViewModel: PermissionViewModel[];
  login_log_seq:number;
  checked:boolean;
  constructor()
  {
  }
}



