import { SingleModelResponse } from "./SingleModelResponse";
import { TableDetailExtViewModel } from "../ViewModel/TableDetailExtViewModel";

export class TableDetailExtResponse extends SingleModelResponse<
  TableDetailExtViewModel
> {
  Model: TableDetailExtViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new TableDetailExtViewModel();
      this.Model.schemaname = obj.Model.schemaname;
      this.Model.tablename = obj.Model.tablename;
      this.Model.tablecname = (obj && obj.Model.tablecname) || null;
      this.Model.columncname = (obj && obj.Model.columncname) || null;
      this.Model.column_id = obj.Model.column_id;
      this.Model.columnname = obj.Model.columnname;
      this.Model.data_type = (obj && obj.Model.data_type) || null;
      this.Model.max_length = obj.Model.max_length;
      this.Model.precision = obj.Model.precision;
      this.Model.scale = obj.Model.scale;
      this.Model.is_pk = (obj && obj.Model.is_pk) || null;
      this.Model.is_uni_cluster = (obj && obj.Model.is_uni_cluster) || null;
      this.Model.is_cluster = (obj && obj.Model.is_cluster) || null;
      this.Model.is_nullable = obj.Model.is_nullable;
      this.Model.default_value = (obj && obj.Model.default_value) || null;
      this.Model.fktable = obj.Model.fktable;
      this.Model.fkcolumnname = obj.Model.fkcolumnname;
      this.Model.fkseq = (obj && obj.Model.fkseq) || null;
      this.Model.fkgroup = obj.Model.fkgroup;
      this.Model.computecolumn = (obj && obj.Model.computecolumn) || null;
      this.Model.is_identity = obj.Model.is_identity;
      this.Model.memo = (obj && obj.Model.memo) || null;
      this.Model.ttype = obj.Model.ttype;
      this.Model.columncname_flag = obj.Model.columncname_flag;
      this.Model.columncname_glossary =
        (obj && obj.Model.columncname_glossary) || null;
      this.Model.columncname_sql = (obj && obj.Model.columncname_sql) || null;
      this.Model.memo_sql = (obj && obj.Model.memo_sql) || null;
    }
  }
}



