import { SingleModelResponse } from "./SingleModelResponse";
import { LineMessageViewModel } from "../ViewModel/LineMessageViewModel";

export class LineMessageResponse extends SingleModelResponse<LineMessageViewModel> {
  Model: LineMessageViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new LineMessageViewModel();
      this.Model.tokenname = obj.Model.tokenname;
      this.Model.subject_no = obj.Model.subject_no;
      this.Model.ssis_job_no = obj.Model.subject_no;
      this.Model.subject = obj.Model.subject;
      this.Model.message = obj.Model.message;
      this.Model.res_message = obj.Model.res_message || null;
      if(obj.Model.message)
      {
        this.Model.short_message = obj.Model.message.substr(0,50);
      }
      else
      this.Model.short_message="";
      if(obj.Model.res_message)
        this.Model.short_res_message = obj.Model.res_message.substr(0,50);
      else
        this.Model.short_res_message="";
      this.Model.is_send_now=obj.Model.is_send_now;
      this.Model.create_time=obj.Model.create_time;
      this.Model.sch_cnt = obj.Model.sch_cnt;
      if (this.Model.sch_cnt > 0) this.Model.sch_color = "danger-left";
        else this.Model.sch_color = "light";
    }
  }
}



