import { MenuViewModel } from "./../Model/ViewModel/MenuViewModel";
import { Component, ViewChild } from "@angular/core";
import {
  Nav,
  Platform,
  MenuController,
  Keyboard,
  App,
  AlertController
} from "ionic-angular";
import { StatusBar } from "@ionic-native/status-bar";
import { SplashScreen } from "@ionic-native/splash-screen";
import { GlobalComponent } from "../components/global/global";
import { AuthServicesProvider } from "../providers/auth-services/auth-services";
import { Network } from "@ionic-native/network";
@Component({
  templateUrl: "app.html"
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;
  init: boolean;
  rootPage: any = "LoginPage";
  public menu_list: MenuViewModel[];
  public all_menu_list: MenuViewModel[];
  pages: Array<{ title: string; component: any }>;
  is_login: boolean;
  public menu_id: string;
  public userName: string;
  public api_name: string;
  public width: number = 0;
  constructor(
    public platform: Platform,
    public statusBar: StatusBar,
    public splashScreen: SplashScreen,
    public menuCtrl: MenuController,
    private keyboard: Keyboard,
    private app: App,
    public alertCtrl: AlertController,
    private global: GlobalComponent,
    public authServices: AuthServicesProvider,
    private network: Network
  ) {
    this.menu_list = [];
    this.initializeApp();
  }
  showExit() {
    let confirm = this.alertCtrl.create({
      title: "結束程式",

      message: "確認要離開應用程式嗎?",

      buttons: [
        {
          text: "取消",

          handler: () => {
            console.log("Disagree clicked");
          }
        },

        {
          text: "確認",

          handler: () => {
            if (this.global.network === true && this.authServices.getUserInfo()!=null ) {
              this.global.createLoader("儲存紀錄中...");
              this.global.loading.present().then(() => {
                this.authServices.logout().subscribe(
                  data => {
                    if (data.DidError === true) {
                      this.global.dismissLoading();
                      this.global.showError(data.ErrorMessage);
                    } else {
                      this.global.dismissLoading();
                    }
                    this.platform.exitApp();
                  },
                  err => {
                    this.global.dismissLoading();
                    this.global.showError(
                      "無法連上WebAPI伺服器，紀錄登出時間-" + err.message
                    );
                    this.platform.exitApp();
                  }
                );
              });
            }
            else
            this.platform.exitApp();
          }
        }
      ]
    });
    confirm.present();
  }

  registerBackButtonAction() {
    this.platform.registerBackButtonAction(() => {
      if (this.keyboard.isOpen()) {
        this.keyboard.close();
        return;
      }

      const overlay = this.app._appRoot._overlayPortal.getActive();
      if (overlay && overlay.dismiss) {
        overlay.dismiss();
        return;
      }

      if (this.nav.canGoBack()) {
        this.nav.pop();
      } else {
        this.showExit();
      }
    });
  }
  initializeApp() {
    this.platform.ready().then(() => {
      this.registerBackButtonAction();
      this.statusBar.styleDefault();
      this.splashScreen.hide();
      this.global.network = true;
      this.network.onDisconnect().subscribe(() => {
        console.log("network was disconnected :-(");
        this.global.network = false;
        this.global.showError("網路斷線!");
      });
      this.network.onConnect().subscribe(() => {
        console.log("network connected!");
        if (this.global.network == false) {
          this.global.network = true;
          this.global.showMessage("網路連線","網路已連線!");
        }
        // We just got a connection but we need to wait briefly
        // before we determine the connection type. Might need to wait.
        // prior to doing any api requests as well.
        // setTimeout(() => {
        //   if (this.network.type === 'wifi') {
        //     console.log('we got a wifi connection, woohoo!');
        //   }
        // }, 3000);
      });
    });
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad app");
  }
  ngAfterViewChecked() {
    this.menuCtrl.enable(true, this.menu_id);
  }
  public SetMenu(ms: MenuViewModel[]) {
    this.menu_list = ms;
  }
  onResize(event) {
    this.width = event.target.innerWidth;
  }
  openPage(page) {
    this.global.createLoader();
    this.global.loading.present().then(() => {
      if (
        page.page_name.indexOf("Management") > -1 ||
        page.page_name.indexOf("Home") > -1
      ) {
        this.nav.setRoot(page.page_name);
      } else this.nav.push(page.page_name);
    });
  }
}



