import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileBrowserListResponse } from "../../Model/Response/FileBrowserListResponse";
import { FileBrowserResponse } from "../../Model/Response/FileBrowserResponse";
import { FileBrowserViewModel } from "../../Model/ViewModel/FileBrowserViewModel";

/*
  Generated class for the FileBrowserServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileBrowserServicesProvider {
  private FileBrowsers: FileBrowserListResponse;
  private FileBrowser: FileBrowserResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "FileBrowser";
    console.log("Hello FileBrowserServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,path:string,order_type:boolean) {
    var sub_url: string;
     sub_url = "FileBrowser?PageSize={1}&PageNumber={2}&keyword={3}&path={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,path,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileBrowsers = new FileBrowserListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileBrowsers;
    });
  }

  public download(filefullname:string) {
    var sub_url: string;
     sub_url = "FileBrowser/Download?filefullname={1}";
    sub_url = String.Format(sub_url, this.ctl, filefullname);
    return this.Services.download(sub_url, this.ctl, true)
    .map(res =>  new Blob([res],{ type: 'application/octet-stream' })
    )

  }
  public upload(data:FormData) {
    var sub_url: string;
     sub_url = "FileBrowser/FormUpload";
    sub_url = String.Format(sub_url, this.ctl);
    return this.Services.Upload(sub_url, this.ctl, true,data)


  }
  public PutAsync(filefullname:string,FileBrowserViewModel:FileBrowserViewModel,mode:string) {
    var sub_url: string;
    //mode:Rename或Copy
    sub_url = "FileBrowser/{2}?filefullname={1}";
    sub_url = String.Format(sub_url, this.ctl,filefullname,mode);
    return this.Services.PutAsync(sub_url, this.ctl, true,FileBrowserViewModel).map(
      item => {
        return new FileBrowserResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

      }
    );
  }



  public DeleteAsync(filefullname:string) {
    var sub_url: string;
    sub_url = "FileBrowser?filefullname={1}";
    sub_url = String.Format(sub_url, this.ctl,filefullname);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.FileBrowser = new FileBrowserResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileBrowser;
      }
    );
  }

}



