import { SingleModelResponse } from "./SingleModelResponse";
import { FileBrowserViewModel } from "../ViewModel/FileBrowserViewModel";

export class FileBrowserResponse extends SingleModelResponse<
  FileBrowserViewModel
> {
  Model: FileBrowserViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      if (obj.Model != null) {
        this.Model = new FileBrowserViewModel();
        this.Model.rename_filename = obj.Model.rename_filename;
        this.Model.ext = obj.Model.ext;
        this.Model.file_size = obj.Model.file_size;
        this.Model.file_size_desc=obj.Model.file_size_desc;
        this.Model.filefullname = obj.Model.filefullname;
        this.Model.filepath = obj.Model.filepath;
        this.Model.create_time = obj.Model.create_time;
      }
    }
  }
}



