import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { DirectoryBrowserListResponse } from "../../Model/Response/DirectoryBrowserListResponse";
import { DirectoryBrowserResponse } from "../../Model/Response/DirectoryBrowserResponse";
import { DirectoryBrowserViewModel } from '../../Model/ViewModel/DirectoryBrowserViewModel';

/*
  Generated class for the DirectoryBrowserServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class DirectoryBrowserServicesProvider {
  private DirectoryBrowsers: DirectoryBrowserListResponse;
  private DirectoryBrowser: DirectoryBrowserResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "DirectoryBrowser";
    console.log("Hello DirectoryBrowserServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,path:string,order_type:boolean) {
    var sub_url: string;
     sub_url = "DirectoryBrowser?PageSize={1}&PageNumber={2}&keyword={3}&path={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,path,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.DirectoryBrowsers = new DirectoryBrowserListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.DirectoryBrowsers;
    });
  }



  public PostPutAsync(path:string,DirectoryBrowserViewModel:DirectoryBrowserViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "DirectoryBrowser";
    else
    sub_url = "DirectoryBrowser?path={1}";
    sub_url = String.Format(sub_url, this.ctl,path);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,DirectoryBrowserViewModel,mode).map(
      item => {
        this.DirectoryBrowser = new DirectoryBrowserResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.DirectoryBrowser;
      }
    );
  }

  public DeleteAsync(path:string) {
    var sub_url: string;
    sub_url = "DirectoryBrowser?path={1}";
    sub_url = String.Format(sub_url, this.ctl,path);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.DirectoryBrowser = new DirectoryBrowserResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.DirectoryBrowser;
      }
    );
  }

  public GetAsync(path:string) {
    var sub_url: string;
    sub_url = "DirectoryBrowser/Get?path={1}";
    sub_url = String.Format(sub_url, this.ctl,path);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        this.DirectoryBrowser = new DirectoryBrowserResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.DirectoryBrowser;
      }
    );
  }

}



