import { JobViewModel } from './JobViewModel';
import { SchViewModel } from './SchViewModel';

export class JobSchViewModel {
  /// <summary>
  ///  作業編號
  /// <summary>
  public ssis_job_no: string;
  /// <summary>
  ///  作業名稱
  /// <summary>
  public ssis_job_name: string;

  /// <summary>
  ///  排程編號
  /// <summary>
  public sch_no: string;
  /// <summary>
  ///  排程名稱
  /// <summary>
  public sch_name: string;
  /// <summary>
  ///  下次執行日期
  /// <summary>
  public next_run_date: string;
  /// <summary>
  ///  下次執行時間
  /// <summary>
  public next_run_time: string;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;
  public checked: boolean;
  /// <summary>
///  遠端連線編號
/// <summary>
public remote_conn_id: string;

/// <summary>
///  連線字串
/// <summary>
public remote_conn_string: string;
  public JobViewModel: JobViewModel;
  public SchViewModel: SchViewModel;
}



