import { JobProcessedSqlListResponse } from "./../../Model/Response/JobProcessedSqlListResponse";
import { String } from "./../../Model/String";
import { Injectable } from "@angular/core";
import { JobProcessedSqlViewModel } from "../../Model/ViewModel/JobProcessedSqlViewModel";
import { JobProcessedSqlResponse } from "../../Model/Response/JobProcessedSqlResponse";
import { ServicesProvider } from "../services/services";
import { StringResponse } from "../../Model/Response/StringResponse";

/*
  Generated class for the jobProcessedSqlServicesProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class JobProcessedSqlServicesProvider {
  ctl: string;
  jobProcessedSqls: JobProcessedSqlListResponse;
  jobProcessedSql: JobProcessedSqlResponse;
  constructor(
    private Services: ServicesProvider
  ) {
    // this.originUrl =apiUrl;
    this.ctl = "jobProcessedSql";

    console.log("Hello jobProcessedSqlServicesProvider Provider");
  }

  GetListsAsync(ssis_job_no: string,PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
    sub_url = "jobProcessedSql?PageSize={1}&PageNumber={2}&keyword={3}&ssis_job_no={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword, ssis_job_no,order_type);
   return this.Services.GetAsync(sub_url,this.ctl,true)
      .map(item => {
        this.jobProcessedSqls = new JobProcessedSqlListResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          PageSize: item.PageSize,
          PageNumber: item.PageNumber,
          TotalRows: item.TotalRows,
          PageRows: item.PageRows,
          TotalPages: item.TotalPages,
          Model: item.Model
        });
        return this.jobProcessedSqls;
      });
  }

  public PostPutAsync(JobProcessedSqlViewModel:JobProcessedSqlViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "JobProcessedSql";
    else
      sub_url = "JobProcessedSql?ssis_job_no={1}&exec_status_key={2}";
    sub_url = String.Format(sub_url, this.ctl, JobProcessedSqlViewModel.ssis_job_no,JobProcessedSqlViewModel.exec_status_key);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,JobProcessedSqlViewModel,mode).map(
      item => {
        this.jobProcessedSql = new JobProcessedSqlResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.jobProcessedSql;
      }
    );
  }

  public DeleteAsync(ssis_job_no:string,exec_status_key:string) {
    var sub_url: string;
    sub_url = "JobProcessedSql?ssis_job_no={1}&exec_status_key={2}";
    sub_url = String.Format(sub_url, this.ctl, ssis_job_no,exec_status_key);
    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.jobProcessedSql = new JobProcessedSqlResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.jobProcessedSql;
      }
    );
  }
  public GetSqlAsync(ssis_job_no:string,exec_status_key:string,method:string) {
    var sub_url: string;
    sub_url = "JobProcessedSql/get_{3}_sql?ssis_job_no={1}&exec_status_key={2}";
    sub_url = String.Format(sub_url, this.ctl,ssis_job_no,exec_status_key,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
}



