import { Injectable } from "@angular/core";
import { ApiUrlListResponse } from "../../Model/Response/ApiUrlListResponse";
import { ApiUrlResponse } from "../../Model/Response/ApiUrlResponse";
import { ApiUrlViewModel } from "../../Model/ViewModel/ApiUrlViewModel";
import { MyAppSharedSettings } from "../../Model/MyAppSharedSettings";
import { Observable } from "rxjs/Observable";
import "rxjs/add/operator/map";
import "rxjs/add/observable/fromPromise";
import { Storage } from "@ionic/storage";
import { ApiUrlRepository } from "../../Model/Repository/ApiUrlRepository";
/*
  Generated class for the ApiUrlServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class ApiUrlServicesProvider {
  private ApiUrls: ApiUrlListResponse;
  private ApiUrl: ApiUrlResponse;
  private ApiUrlRep: ApiUrlRepository;
  ctl: string;

  constructor(public conf: MyAppSharedSettings, public storage: Storage) {
    this.ApiUrlRep = new ApiUrlRepository();
   // this.storage.set("API_URL_LISTS", null);
    console.log("Hello ApiUrlServiceProvider Provider");
  }
  private save_api_list()
  {
    let ApiUrlView = this.ApiUrlRep.List("", true);
    this.storage.set("API_URL_LISTS", JSON.stringify(ApiUrlView));
  }

  public Set_api_list(api_url_list:string)
  {
    this.storage.set("API_URL_LISTS", api_url_list);
  }
  public GetListsAsync(keyword: string, order_type: boolean) {
    return Observable.fromPromise(
      this.storage.get("API_URL_LISTS").then(val => {
        if (val != null) {
          let ApiUrlView: ApiUrlViewModel[];
          ApiUrlView = JSON.parse(val);
          this.ApiUrlRep = new ApiUrlRepository();
          this.ApiUrlRep.AddList(ApiUrlView);
          this.ApiUrls = new ApiUrlListResponse({
            Message: "查詢成功",
            DidError: false,
            ErrorMessage: "",
            PageSize: 24,
            PageNumber: 1,
            TotalRows: 1,
            PageRows: 1,
            TotalPages: 1,
            Token:"",
            Model: this.ApiUrlRep.List(keyword, order_type)
          });
          return this.ApiUrls;
        } else {
          let item = new ApiUrlViewModel();
          item.id = "sys";
          item.disp_name = "系統建立";
          item.url = this.conf.API_URL;
          item.is_default = true;
          item.is_url_forward = this.conf.is_url_forward;
          this.ApiUrlRep.Add(item);
         this.save_api_list();
          this.ApiUrls = new ApiUrlListResponse({
            Message: "查詢成功",
            DidError: false,
            ErrorMessage: "",
            PageSize: 24,
            PageNumber: 1,
            TotalRows: 1,
            PageRows: 1,
            TotalPages: 1,
            Token:"",
            Model: this.ApiUrlRep.List(keyword, order_type)
          });
          return this.ApiUrls;
        }
      })
    );
  }
  public PostPutAsync(item: ApiUrlViewModel, mode: string) {
    if (mode === "POST") {
      this.ApiUrlRep.Add(item);
    } else {
      this.ApiUrlRep.Update(item);
    }
    this.save_api_list();
    return Observable.fromPromise(
      this.storage.get("").then(val => {
        this.ApiUrl = new ApiUrlResponse({
          Message: "查詢成功",
          DidError: false,
          ErrorMessage: "",
          Model: item,
          Token:"",
        });
        return this.ApiUrl;
      })
    );
  }

  public DeleteAsync(id: string) {
    this.ApiUrlRep.Del(id);
    this.save_api_list();
    return Observable.fromPromise(
      this.storage.get("").then(val => {
        this.ApiUrl = new ApiUrlResponse({
          Message: "刪除成功",
          DidError: false,
          ErrorMessage: "",
          Model: "",
          Token:"",
        });
        return this.ApiUrl;
      })
    );
  }
}



