export class TableDetailExtViewModel
{
  /// <summary>
///  結構描述
/// <summary>
public schemaname: string;
/// <summary>
///  資料表
/// <summary>
public tablename: string;
/// <summary>
///  資料表說明
/// <summary>
public tablecname: string;
/// <summary>
///  欄位說明
/// <summary>
public columncname: string;
/// <summary>
///  欄位編號
/// <summary>
public column_id: number;
/// <summary>
///  欄位名稱
/// <summary>
public columnname: string;
/// <summary>
///  資料型態
/// <summary>
public data_type: string;
/// <summary>
///  最大長度
/// <summary>
public max_length: number;
/// <summary>
///  整數
/// <summary>
public precision: number;
/// <summary>
///  小數
/// <summary>
public scale: number;
/// <summary>
///  是否主鍵
/// <summary>
public is_pk: number;
/// <summary>
///  叢集唯一
/// <summary>
public is_uni_cluster: number;
/// <summary>
///  叢集非唯一
/// <summary>
public is_cluster: number;
/// <summary>
///  空值
/// <summary>
public is_nullable: string;
/// <summary>
///  預設值
/// <summary>
public default_value: string;
/// <summary>
///  參考資料表
/// <summary>
public fktable: string;
/// <summary>
///  參考欄位
/// <summary>
public fkcolumnname: string;
/// <summary>
///  參考欄位順序
/// <summary>
public fkseq: number;
/// <summary>
///  參考群組
/// <summary>
public fkgroup: string;
/// <summary>
///  計算欄位
/// <summary>
public computecolumn: string;
/// <summary>
///  自動累加
/// <summary>
public is_identity: string;
/// <summary>
///  備註說明
/// <summary>
public memo: string;
/// <summary>
///  資料表類型
/// <summary>
public ttype: string;
/// <summary>
///
/// <summary>
public columncname_flag: string;
/// <summary>
/// 資料字典中文名稱
/// <summary>
public columncname_glossary: string;
/// <summary>
/// 中文欄位sql
/// <summary>
public columncname_sql: string;
/// <summary>
/// 備註sql
/// <summary>
public memo_sql: string;

/// <summary>
///  資料表全名
/// <summary>
public tablefullname: string;
}



