import {StringResponse} from '../../Model/Response/StringResponse';
import {NumberResponse} from '../../Model/Response/NumberResponse';
import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";

/*
  Generated class for the FileServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileServicesProvider {
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "File";
    console.log("Hello FileServicesProvider Provider");
  }



  public DeleteAsync(exec_file_seq:string) {
    var sub_url: string;
    sub_url = "File/{1}";
    sub_url = String.Format(sub_url, this.ctl,exec_file_seq);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: ""
        });
      }
    );
  }
  public GetMaxExecFileSeqAsync(func_no:number,exec_group:string) {
    var sub_url: string;
    sub_url = "File/get_max_exec_file_seq?func_no={1}&exec_group={2}&min_exec_file_seq=&max_exec_file_seq=";
    sub_url = String.Format(sub_url, this.ctl,func_no,exec_group);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new NumberResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
  public GetMaxRangeExecFileSeqAsync(func_no:number,exec_group:string,min_exec_file_seq:number,max_exec_file_seq:number) {
    var sub_url: string;
    sub_url = "File/get_max_exec_file_seq?func_no={1}&exec_group={2}&min_exec_file_seq={3}&max_exec_file_seq={4}";
    sub_url = String.Format(sub_url, this.ctl,func_no,exec_group,min_exec_file_seq,max_exec_file_seq);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new NumberResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
  public GetMaxExecSeqAsync(func_no:number,exec_group:string) {
    var sub_url: string;
    sub_url = "File/get_max_exec_seq?func_no={1}&exec_group={2}";
    sub_url = String.Format(sub_url, this.ctl,func_no,exec_group);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new NumberResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }


  public GetSqlAsync(exec_file_seq:string,method:string) {
    var sub_url: string;
    sub_url = "File/get_{2}_sql?exec_file_seq={1}";
    sub_url = String.Format(sub_url, this.ctl,exec_file_seq,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
}



