import { ConnViewModel } from "../ViewModel/ConnViewModel";
import { ListModelResponse } from "./ListModelResponse";

export class ConnListResponse extends ListModelResponse<ConnViewModel> {
  Model: ConnViewModel[];
  constructor(obj?: any) {
    super(obj);
    this.Model = [];
    this.Message = obj.Messag;
    this.DidError = obj.DidError;
    this.ErrorMessage = (obj && obj.ErrorMessage) || null;
    this.PageNumber = obj.PageNumber;
    this.PageSize = obj.PageSize;
    this.PageRows = obj.PageRows;
    this.TotalPages = obj.TotalPages;
    this.TotalRows = obj.TotalRows;
    if (this.DidError === false) {
      obj.Model.forEach(data => {
        let item = new ConnViewModel();
        item.remote_conn_id = data.remote_conn_id;
        item.db_provider = (data && data.db_provider) || null;
        item.conn_string = data.conn_string;
        item.pwd = (data && data.pwd) || null;
        item.creator = data.creator;
        item.create_time = data.create_time;
        item.modifier = data.modifier;
        item.last_update_time = data.last_update_time;
        item.conn_type_key = data.conn_type_key;
        item.conn_type = data.conn_type;
        this.Model.push(item);
      }) || null;
    }
  }
}



