import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { JobSchListResponse } from "../../Model/Response/JobSchListResponse";
import { JobSchViewModel } from "../../Model/ViewModel/JobSchViewModel";
import { JobSchResponse } from "../../Model/Response/JobSchResponse";

/*
  Generated class for the JobSchServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class JobSchServicesProvider {
  private JobSchs: JobSchListResponse;
  private JobSch: JobSchResponse;
  ctl: string;

  constructor(public Services: ServicesProvider) {
    this.ctl = "JobSch";
    console.log("Hello JobSchServiceProvider Provider");
  }
  public GetListsAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
    sub_url = "JobSch?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
    return this.Services.GetAsync(sub_url, this.ctl, true).map(item => {
      this.JobSchs = new JobSchListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.JobSchs;
    });
  }
  public GetSchListsAsync(
    PageSize: number,
    PageNumber: number,
    keyword: string,
    ssis_job_no: string,
    order_type:boolean,
  ) {
    var sub_url: string;
    sub_url = "JobSch?PageSize={1}&PageNumber={2}&keyword={3}&ssis_job_no={4}&order_type={5}";
    sub_url = String.Format(
      sub_url,
      this.ctl,
      PageSize,
      PageNumber,
      keyword,
      ssis_job_no,
      order_type,
    );
    return this.Services.GetAsync(sub_url, this.ctl, true).map(item => {
      this.JobSchs = new JobSchListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.JobSchs;
    });
  }
  public GetJobListsAsync(
    PageSize: number,
    PageNumber: number,
    keyword: string,
    sch_no: string,
    order_type:boolean,
  ) {
    var sub_url: string;
    sub_url = "JobSch?PageSize={1}&PageNumber={2}&keyword={3}&sch_no={4}&order_type={5}";
    sub_url = String.Format(
      sub_url,
      this.ctl,
      PageSize,
      PageNumber,
      keyword,
      sch_no,
      order_type,
    );
    return this.Services.GetAsync(sub_url, this.ctl, true).map(item => {
      this.JobSchs = new JobSchListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.JobSchs;
    });
  }
  public GetAsync(Job_no: string, sch_no: string) {
    var sub_url: string;
    sub_url = "JobSch/job_no={1}&sch_no={2}";
    sub_url = String.Format(sub_url, this.ctl, Job_no, sch_no);
    return this.Services.GetAsync(sub_url, this.ctl, true).map(item => {
      this.JobSch = new JobSchResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        Model: item.Model
      });
      return this.JobSch;
    });
  }

  public PostAsync(JobSchViewModel: JobSchViewModel) {
    var sub_url: string;
    sub_url = "JobSch";
    return this.Services.PostAsync(
      sub_url,
      this.ctl,
      true,
      JobSchViewModel
    ).map(item => {
      this.JobSch = new JobSchResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        Model: item.Model
      });
      return this.JobSch;
    });
  }
  public PostListAsync(JobSchViewModel: JobSchViewModel[]) {
    var sub_url: string;
    sub_url = "JobSch/AddList";

    return this.Services.PostAsync(
      sub_url,
      this.ctl,
      true,
      JobSchViewModel
    ).map(item => {
      this.JobSch = new JobSchResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        Model: item.Model
      });
      return this.JobSch;
    });
  }
  public PutAsync(JobSchViewModel: JobSchViewModel) {
    var sub_url: string;
    sub_url = "JobSch?ssis_job_no={1}&sch_no={2}";
    sub_url = String.Format(
      sub_url,
      this.ctl,
      JobSchViewModel.ssis_job_no,
      JobSchViewModel.sch_no
    );
    return this.Services.PutAsync(sub_url, this.ctl, true, JobSchViewModel).map(
      item => {
        this.JobSch = new JobSchResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.JobSch;
      }
    );
  }
  public DeleteAsync(ssis_job_no: string, sch_no: string) {
    var sub_url: string;
    sub_url = "JobSch?ssis_job_no={1}&sch_no={2}";
    sub_url = String.Format(sub_url, this.ctl, ssis_job_no, sch_no);
    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(item => {
      this.JobSch = new JobSchResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        Model: item.Model
      });

      return this.JobSch;
    });
  }
}



