import {StringResponse} from '../../Model/Response/StringResponse';
import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { ProgListResponse } from "../../Model/Response/ProgListResponse";
import { ProgResponse } from "../../Model/Response/ProgResponse";
import { ProgViewModel } from '../../Model/ViewModel/ProgViewModel';

/*
  Generated class for the ProgServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class ProgServicesProvider {
  private Progs: ProgListResponse;
  private Prog: ProgResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "Prog";
    console.log("Hello ProgServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number,schemaname:string,name:string,type:string, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "Prog?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}&pschemaname={5}&pname={6}&type={7}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type,schemaname,name,type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Progs = new ProgListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Progs;
    });
  }



  public PostPutAsync(ProgViewModel:ProgViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "Prog";
    else
    sub_url = "Prog?schemaname={1}&name={2}";
    sub_url = String.Format(sub_url, this.ctl,ProgViewModel.schemaname,ProgViewModel.name);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,ProgViewModel,mode).map(
      item => {
        this.Prog = new ProgResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.Prog;
      }
    );
  }



  public DeleteAsync(schemaname:string,name:string) {
    var sub_url: string;
    sub_url = "Prog?schemaname={1}&name={2}";
    sub_url = String.Format(sub_url, this.ctl,schemaname,name);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.Prog = new ProgResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.Prog;
      }
    );
  }


  public GetSqlAsync(schemaname:string,name:string) {
    var sub_url: string;
    sub_url = "Prog/get?schemaname={1}&name={2}";
    sub_url = String.Format(sub_url, this.ctl,schemaname,name);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }

}



