import { SingleModelResponse } from "./SingleModelResponse";
import { ProgViewModel } from "../ViewModel/ProgViewModel";

export class ProgResponse extends SingleModelResponse<ProgViewModel> {
  Model: ProgViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new ProgViewModel();
      this.Model.schemaname = obj.Model.schemaname;
      this.Model.name = obj.Model.name;
      this.Model.object_desc = (obj && obj.Model.object_desc) || null;
      this.Model.type = (obj && obj.Model.type) || null;
      this.Model.type_desc = (obj && obj.Model.type_desc) || null;
      this.Model.ttype = (obj && obj.Model.ttype) || null;
      this.Model.drop_sql = (obj && obj.Model.drop_sql) || null;
      this.Model.create_sql = (obj && obj.Model.create_sql) || null;
      this.Model.create_date = (obj && obj.Model.create_date) || null;
      this.Model.modify_date = (obj && obj.Model.modify_date) || null;
      this.Model.category = (obj && obj.Model.category) || null;
    }
  }
}



