import { SingleModelResponse } from './SingleModelResponse';
import { UserRoleViewModel } from '../ViewModel/UserRoleViewModel';


export class UserRoleResponse extends SingleModelResponse<UserRoleViewModel> {
      Model: UserRoleViewModel;
      constructor(obj?: any)
      {
        super(obj);
        this.Message= obj.Message ;
        this.DidError=obj.DidError;
        this.ErrorMessage=obj.ErrorMessage ;
        if(this.DidError===false)
        {
          this.Model= new UserRoleViewModel();
          this.Model.userName=obj.Model.userName;
          this.Model.roleName=obj.Model.roleName;
        }
      }
  }





