import {StringResponse} from '../../Model/Response/StringResponse';
import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { ConnListResponse } from "../../Model/Response/ConnListResponse";
import { ConnResponse } from "../../Model/Response/ConnResponse";
import { ConnFtpListResponse } from '../../Model/Response/ConnFtpListResponse';
import { ConnSmtpListResponse } from '../../Model/Response/ConnSmtpListResponse';
import { ConnHttpListResponse } from '../../Model/Response/ConnHttpListResponse';

/*
  Generated class for the ConnServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class ConnServicesProvider {
  private Conns: ConnListResponse;
  private Conn: ConnResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "Conn";
    console.log("Hello ConnServiceProvider Provider");
  }
  public GetIDListsByAsync() {
    var sub_url: string;
     sub_url = "Conn?PageSize=0&PageNumber=0";
    sub_url = String.Format(sub_url, this.ctl);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Conns = new ConnListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Conns;
    });
  }
  public GetListsHttpByAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "ConnHttp?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Conns = new ConnHttpListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Conns;
    });
  }
  public GetListsLineByAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "ConnLine?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Conns = new ConnHttpListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Conns;
    });
  }
  public GetListsFtpByAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "ConnFtp?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Conns = new ConnFtpListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Conns;
    });
  }
  public GetListsSmtpByAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "ConnSmtp?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Conns = new ConnSmtpListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Conns;
    });
  }
  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,conn_type_key:string,order_type:boolean) {
    var sub_url: string;
     sub_url = "Conn?PageSize={1}&PageNumber={2}&keyword={3}&conn_type_key={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,conn_type_key,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Conns = new ConnListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Conns;
    });
  }

  public PostPutLineAsync(ConnViewModel,mode:string) {
    var sub_url: string;
    var func:string="Line";

    if(mode==="POST" )
    {
      sub_url = "Conn{2}";
    }
    else
    sub_url = "Conn{2}/{1}";
    sub_url = String.Format(sub_url, this.ctl,ConnViewModel.remote_conn_id,func);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,ConnViewModel,mode).map(
      item => {
        this.Conn = new ConnResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.Conn;
      }
    );
  }

  public PostPutAsync(ConnViewModel,mode:string,conn_type_key:string) {
    var sub_url: string;
    var func:string;
    if (conn_type_key === "065|000")
    func = "";
  if (conn_type_key === "065|001" || conn_type_key === "065|006")
    func = "DB";
  if (conn_type_key === "065|002")
    func = "Ftp";
  if (conn_type_key === "065|003")
    func = "Smtp";
  if (conn_type_key === "065|004")
    func = "Http";
    if (conn_type_key === "065|005")
    func = "Sbp";
    if(mode==="POST" )
    {
      sub_url = "Conn{2}";
    }
    else
    sub_url = "Conn{2}/{1}";
    sub_url = String.Format(sub_url, this.ctl,ConnViewModel.remote_conn_id,func);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,ConnViewModel,mode).map(
      item => {
        this.Conn = new ConnResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.Conn;
      }
    );
  }

  public DeleteAsync(remote_conn_id:string) {
    var sub_url: string;
    sub_url = "Conn/{1}";
    sub_url = String.Format(sub_url, this.ctl,remote_conn_id);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.Conn = new ConnResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.Conn;
      }
    );
  }
  public GetConnAsync(remote_conn_id:string) {
    var sub_url: string;
    sub_url = "Conn/{1}";
    sub_url = String.Format(sub_url, this.ctl,remote_conn_id);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new ConnResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }


  public GetSqlAsync(remote_conn_id:string,method:string) {
    var sub_url: string;
    sub_url = "Conn/get_{2}_sql?remote_conn_id={1}";
    sub_url = String.Format(sub_url, this.ctl,remote_conn_id,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
   public GetLineUrlAsync(data_action:string) {
    var sub_url: string;
    sub_url = "ConnLine/GetLineUrl?data_action={1}";
    sub_url = String.Format(sub_url, this.ctl,data_action);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
}



