import { SingleModelResponse } from "./SingleModelResponse";
import { FilePathViewModel } from "../ViewModel/FilePathViewModel";

export class FilePathResponse extends SingleModelResponse<FilePathViewModel> {
  Model: FilePathViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new FilePathViewModel();
      this.Model.exec_file_seq=obj.Model.exec_file_seq;
      this.Model.file_path_type_key=obj.Model.file_path_type_key;
      this.Model.file_path_type=obj.Model.file_path_type;
      this.Model.file_path_key=obj.Model.file_path_key;
      this.Model.file_path=obj.Model.file_path;
      this.Model.creator=obj.Model.creator;
      this.Model.create_time=obj.Model.create_time;
      this.Model.modifier=obj.Model.modifier;
      this.Model.last_update_time=obj.Model.last_update_time;


    }
  }
}



