import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { ProgramPageMenuListResponse } from "../../Model/Response/ProgramPageMenuListResponse";
import { ProgramPageMenuResponse } from "../../Model/Response/ProgramPageMenuResponse";
import { ProgramPageMenuViewModel } from "../../Model/ViewModel/ProgramPageMenuViewModel";

/*
  Generated class for the ProgramPageMenuServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class ProgramPageMenuServicesProvider {
  private ProgramPageMenus: ProgramPageMenuListResponse;
  private ProgramPageMenu: ProgramPageMenuResponse;
  ctl: string;

  constructor(public Services: ServicesProvider) {
    this.ctl = "ProgramPageMenu";
    console.log("Hello ProgramPageMenuServiceProvider Provider");
  }
  public GetListsAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
    sub_url = "ProgramPageMenu?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
    return this.Services.GetAsync(sub_url, this.ctl, true).map(item => {
      this.ProgramPageMenus = new ProgramPageMenuListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.ProgramPageMenus;
    });
  }
  public GetPageListsAsync(
    PageSize: number,
    PageNumber: number,
    keyword: string,
    menu_no: string
    ,order_type:boolean
  ) {
    var sub_url: string;
    sub_url =
      "ProgramPageMenu?PageSize={1}&PageNumber={2}&keyword={3}&menu_no={4}&order_type={5}";
    sub_url = String.Format(
      sub_url,
      this.ctl,
      PageSize,
      PageNumber,
      keyword,
      menu_no,
      order_type,
    );
    return this.Services.GetAsync(sub_url, this.ctl, true).map(item => {
      this.ProgramPageMenus = new ProgramPageMenuListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.ProgramPageMenus;
    });
  }
  public GetMenuListsAsync(
    PageSize: number,
    PageNumber: number,
    keyword: string,
    page_name: string
    ,order_type:boolean
  ) {
    var sub_url: string;
    sub_url =
      "ProgramPageMenu?PageSize={1}&PageNumber={2}&keyword={3}&page_name={4}&order_type={5}";
    sub_url = String.Format(
      sub_url,
      this.ctl,
      PageSize,
      PageNumber,
      keyword,
      page_name,
      order_type,
    );
    return this.Services.GetAsync(sub_url, this.ctl, true).map(item => {
      this.ProgramPageMenus = new ProgramPageMenuListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.ProgramPageMenus;
    });
  }

  public GetAsync(page_name: string, menu_no: string) {
    var sub_url: string;
    sub_url = "ProgramPageMenu?page_name={1}&menu_no={2}";
    sub_url = String.Format(sub_url, this.ctl, page_name, menu_no);
    return this.Services.GetAsync(sub_url, this.ctl, true).map(item => {
      this.ProgramPageMenu = new ProgramPageMenuResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        Model: item.Model
      });
      return this.ProgramPageMenu;
    });
  }

  public PostPutAsync(
    ProgramPageMenuViewModel: ProgramPageMenuViewModel,
    mode: string
  ) {
    var sub_url: string;
    if (mode === "POST") sub_url = "ProgramPageMenu";
    else sub_url = "ProgramPageMenu?page_name={1}&menu_no={2}";
    sub_url = String.Format(
      sub_url,
      this.ctl,
      ProgramPageMenuViewModel.page_name,
      ProgramPageMenuViewModel.menu_no
    );
    return this.Services.PostPutAsync(
      sub_url,
      this.ctl,
      true,
      ProgramPageMenuViewModel,
      mode
    ).map(item => {
      this.ProgramPageMenu = new ProgramPageMenuResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        Model: item.Model
      });
      return this.ProgramPageMenu;
    });
  }
  public PostAsync(ProgramPageMenuViewModel: ProgramPageMenuViewModel) {
    var sub_url: string;
    sub_url = "ProgramPageMenu";
    return this.Services.PostAsync(
      sub_url,
      this.ctl,
      true,
      ProgramPageMenuViewModel
    ).map(item => {
      this.ProgramPageMenu = new ProgramPageMenuResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        Model: item.Model
      });
      return this.ProgramPageMenu;
    });
  }

  public PostListAsync(ProgramPageMenuViewModel: ProgramPageMenuViewModel[]) {
    var sub_url: string;
    sub_url = "ProgramPageMenu/AddList";

    return this.Services.PostAsync(
      sub_url,
      this.ctl,
      true,
      ProgramPageMenuViewModel
    ).map(item => {
      this.ProgramPageMenu = new ProgramPageMenuResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        Model: item.Model
      });
      return this.ProgramPageMenu;
    });
  }
  public PutAsync(ProgramPageMenuViewModel: ProgramPageMenuViewModel) {
    var sub_url: string;
    sub_url = "ProgramPageMenu?page_name={1}&menu_no={2}";
    sub_url = String.Format(
      sub_url,
      this.ctl,
      ProgramPageMenuViewModel.page_name,
      ProgramPageMenuViewModel.menu_no
    );

    return this.Services.PutAsync(
      sub_url,
      this.ctl,
      true,
      ProgramPageMenuViewModel
    ).map(item => {
      this.ProgramPageMenu = new ProgramPageMenuResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        Model: item.Model
      });
      return this.ProgramPageMenu;
    });
  }

  public DeleteAsync(page_name: string, menu_no: string) {
    var sub_url: string;
    sub_url = "ProgramPageMenu?page_name={1}&menu_no={2}";
    sub_url = String.Format(sub_url, this.ctl, page_name, menu_no);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(item => {
      this.ProgramPageMenu = new ProgramPageMenuResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        Model: item.Model
      });
      return this.ProgramPageMenu;
    });
  }
}



