import { UserRoleViewModel } from './UserRoleViewModel';
export class PermissionViewModel {
  /// <summary>
  ///  權限編號
  /// <summary>
  public permission_no: string;
  /// <summary>
  ///  權限說明
  /// <summary>
  public permission_desc: string;
  /// <summary>
  ///  權限詳述
  /// <summary>
  public permission_func_desc: string;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;

  /// <summary>
  /// 使用者
  /// </summary>
  public users: UserRoleViewModel[];
  checked: boolean;
}



