import { ApiUrlViewModel } from "../ViewModel/ApiUrlViewModel";
import { ListModelResponse } from "./ListModelResponse";

export class ApiUrlListResponse extends ListModelResponse<ApiUrlViewModel> {
  Model: ApiUrlViewModel[];
  constructor(obj?: any) {
    super(obj);
    this.Model = [];
    this.Message = obj.Messag;
    this.DidError = obj.DidError;
    this.ErrorMessage = (obj && obj.ErrorMessage) || null;
    this.PageNumber = obj.PageNumber;
    this.PageSize = obj.PageSize;
    this.PageRows = obj.PageRows;
    this.TotalPages = obj.TotalPages;
    this.TotalRows = obj.TotalRows;
    if (this.DidError === false) {
      if (obj.Model != null) {
        obj.Model.forEach(data => {
          let item = new ApiUrlViewModel();
          item.id = data.id;
          item.url = data.url;
          item.disp_name = data.disp_name;
          item.is_default = data.is_default;
          item.is_url_forward = data.is_url_forward;
          if(data.is_url_forward===true)
            item.url_forward="轉址";
          else
            item.url_forward="";
          this.Model.push(item);
        }) || null;
      }
    }
  }
}



