import { SingleModelResponse } from "./SingleModelResponse";
import { TableExtViewModel } from "../ViewModel/TableExtViewModel";

export class TableExtResponse extends SingleModelResponse<TableExtViewModel> {
  Model: TableExtViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new TableExtViewModel();
      this.Model.schemaname=obj.Model.schemaname;
      this.Model.tablename=obj.Model.tablename;
      this.Model.tablefullname=obj.Model.schemaname+'.' +obj.Model.tablename;
      this.Model.tablecname=obj && obj.Model.tablecname || null;
      this.Model.category=obj && obj.Model.category || null;
      this.Model.tablecname_flag=obj && obj.Model.tablecname_flag || null;
      this.Model.category_flag=obj && obj.Model.category_flag || null;
      this.Model.tablecname_glossary=obj && obj.Model.tablecname_glossary || null;
      this.Model.ttype=obj.Model.ttype;
      this.Model.tablecname_sql=obj && obj.Model.tablecname_sql || null;
      this.Model.category_sql=obj && obj.Model.category_sql || null;
      this.Model.table_set_sql=obj && obj.Model.table_set_sql || null;
      this.Model.copy_table_set_sql=obj && obj.Model.copy_table_set_sql || null;

    }
  }
}



