export class ConnViewModel
{
  /// <summary>
///  遠端連線編號
/// <summary>
public remote_conn_id: string;
/// <summary>
///  資料庫提供者
/// <summary>
public db_provider: string;
/// <summary>
///  連線字串
/// <summary>
public conn_string: string;
/// <summary>
///  密碼加密
/// <summary>
public pwd: any;
/// <summary>
///  建立者
/// <summary>
public creator: string;
/// <summary>
///  建立時間
/// <summary>
public create_time: Date;
/// <summary>
///  最後更新者
/// <summary>
public modifier: string;
/// <summary>
///  最後更新時間
/// <summary>
public last_update_time: Date;
/// <summary>
///  連線類型主鍵
/// <summary>
public conn_type_key: string;
/// <summary>
///  連線類型
/// <summary>
public conn_type: string;

}



