import { user_menuViewModel } from "../ViewModel/user_menuViewModel";
import { SingleModelResponse } from "./SingleModelResponse";
import { UserViewModel } from "../ViewModel/UserViewModel";
import { MenuViewModel } from "../ViewModel/MenuViewModel";
import { ProgramPageMenuViewModel } from "../ViewModel/ProgramPageMenuViewModel";
import { ProgramPageViewModel } from "../ViewModel/ProgramPageViewModel";
import { PermissionViewModel } from "../ViewModel/PermissionViewModel";

export class UserMenuResponse extends SingleModelResponse<user_menuViewModel> {
  Model: user_menuViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;

    if (this.DidError === false) {
      this.Model = new user_menuViewModel();
      this.Model.lic=obj.Model.lic;
      this.Model.table_lic=obj.Model.table_lic;
      this.Model.batch_lic=obj.Model.batch_lic;
      this.Model.batch_edit_lic=obj.Model.batch_edit_lic;
      this.Model.sch_lic=obj.Model.sch_lic;
      this.Model.file_browser_lic=obj.Model.file_browser_lic;
      this.Model.ftp_file_browser_lic=obj.Model.ftp_file_browser_lic;
      this.Model.remote_conn_id=obj.Model.remote_conn_id;
      this.Model.conn_type_key=obj.Model.conn_type_key;
      this.Model.conn_type=obj.Model.conn_type;
      this.Model.encrypt_method_key=obj.Model.encrypt_method_key;
      this.Model.encrypt_method=obj.Model.encrypt_method;
      let user = new UserViewModel();

      user.userName = obj.Model.userViewModel.userName;
      user.password = obj.Model.userViewModel.password;
      user.Email = obj.Model.userViewModel.Email || null;
      user.token=obj.Model.userViewModel.token;
      user.login_log_seq=obj.Model.userViewModel.login_log_seq;
      user.roleName = [];
      user.PermissionViewModel = [];

      if (obj != null && obj.Model.userViewModel != null) {
        obj.Model.userViewModel.roleName.forEach(element => {
          user.roleName.push(element);
        });
        if (obj.Model.userViewModel.permissionViewModel != null) {
          obj.Model.userViewModel.permissionViewModel.forEach(element => {
            let p = new PermissionViewModel();
            p.permission_no = element.permission_no;
            p.permission_desc = element.permission_desc;
            p.permission_func_desc = element.permission_func_desc;
            p.creator = element.creator;
            p.create_time = element.create_time;
            p.modifier = element.modifier;
            p.last_update_time = element.last_update_time;
            user.PermissionViewModel.push(p);
          });
        }
      }

      this.Model.UserViewModel = user;
      this.Model.MenuViewModel = [];
      obj.Model.menuViewModel.forEach(data => {
        let item = new MenuViewModel();
        item.menu_id=data.menu_no;
        item.menu_no = data.menu_no;
        item.menu_desc = data.menu_desc;
        item.ProgramPageMenuViewModel = [];
        data.program_page_menuViewModel.forEach(pm => {
          let pm_item = new ProgramPageMenuViewModel();
          pm_item.menu_no = pm.menu_no;
          pm_item.page_name = pm.page_name;
          let pmp_item = new ProgramPageViewModel();
          pmp_item.page_name =pm.program_pageViewModel.page_name
          pmp_item.page_desc =pm.program_pageViewModel.page_desc
          pmp_item.program_no =pm.program_pageViewModel.program_no
          pm_item.ProgramPageViewModel = pmp_item;
          item.ProgramPageMenuViewModel.push(pm_item);
        });
        this.Model.MenuViewModel.push(item);
      }) || null;
    }
  }
}



