import { UserViewModel } from './../../Model/ViewModel/UserViewModel';
import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { UserListResponse } from "../../Model/Response/UserListResponse";
import { UserResponse } from "../../Model/Response/UserResponse";

/*
  Generated class for the UserServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class UserServicesProvider {
  private Users: UserListResponse;
  private User: UserResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "User";
    console.log("Hello UserServiceProvider Provider");
  }
  public GetAddSelectListsAsync(PageSize: number, PageNumber: number,userName:string, roleName: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "User?PageSize={1}&PageNumber={2}&userName={3}&roleName={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber,userName, roleName,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Users = new UserListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Users;
    });
  }

  public GetListsAsync(PageSize: number, PageNumber: number,userName:string,order_type:boolean) {
    var sub_url: string;
     sub_url = "User?PageSize={1}&PageNumber={2}&userName={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber,userName,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Users = new UserListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Users;
    });
  }

  public GetAsync(userName:string) {
    var sub_url: string;
     sub_url = "User?id={1}";
    sub_url = String.Format(sub_url, this.ctl,userName);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.User = new UserResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        Model: item.Model
      });
      return this.User;
    });
  }

  public PostPutAsync(UserViewModel:UserViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "User";
    else
      sub_url = "User/" + UserViewModel.userName ;
    return this.Services.PostPutAsync(sub_url, this.ctl, true,UserViewModel,mode).map(
      item => {
        this.User = new UserResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.User;
      }
    );
  }
  public PostAsync(User:UserViewModel) {
    var sub_url: string;
    sub_url = "User";
    return this.Services.PostAsync(sub_url, this.ctl, true,User).map(
      item => {
        this.User = new UserResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.User;
      }
    );
  }
  public DeleteAsync(userName:string) {
    var sub_url: string;
    sub_url =String.Format("User?id={0}",userName);
    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.User = new UserResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.User;
      }
    );
  }




}



