import { ProgramPageMenuViewModel } from "./ProgramPageMenuViewModel";
import { ProgramViewModel } from "./ProgramViewModel";

export class ProgramPageViewModel {
         /// <summary>
         ///  程式頁面名稱
         /// <summary>
         public page_name: string;
         /// <summary>
         ///  程式頁面說明
         /// <summary>
         public page_desc: string;
         /// <summary>
         ///  程式編號
         /// <summary>
         public program_no: string;
         /// <summary>
         ///  建立者
         /// <summary>
         public creator: string;
         /// <summary>
         ///  建立時間
         /// <summary>
         public create_time: Date;
         /// <summary>
         ///  最後更新者
         /// <summary>
         public modifier: string;
         /// <summary>
         ///  最後更新時間
         /// <summary>
         public last_update_time: Date;
         public checked: boolean;
         public ProgramViewModel: ProgramViewModel;
         public ProgramPageMenuViewModel: ProgramPageMenuViewModel[];
       }



