import { SingleModelResponse } from "./SingleModelResponse";
import { ProgramViewModel } from "../ViewModel/ProgramViewModel";

export class ProgramResponse extends SingleModelResponse<
  ProgramViewModel
> {
  Model: ProgramViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new ProgramViewModel();
      this.Model.program_no=obj.Model.program_no;
      this.Model.program_desc=obj.Model.program_desc;
      this.Model.program_name=obj.Model.program_name;
      this.Model.bus_type_key=obj.Model.bus_type_key;
      this.Model.program_type_key=obj.Model.program_type_key;
      this.Model.program_type_seq=obj.Model.program_type_seq;
      this.Model.program_path_key=obj && obj.Model.program_path_key || null;
      this.Model.master_program_no=obj && obj.Model.master_program_no || null;
      this.Model.parent_program_no=obj && obj.Model.parent_program_no || null;
      this.Model.menu_key=obj && obj.Model.menu_key || null;
      this.Model.audit_flag=obj && obj.Model.audit_flag || null;
      this.Model.creator=obj.Model.creator;
      this.Model.create_time=obj.Model.create_time;
      this.Model.modifier=obj.Model.modifier;
      this.Model.last_update_time=obj.Model.last_update_time;
      this.Model.sql_statement=obj.Model.sql_statement;
      this.Model.is_active = obj.Model.is_active;
      this.Model.is_active_desc = obj.Model.is_active_desc;
      this.Model.bus_type=obj.Model.bus_type;
      switch (obj.Model.is_active) {
        case false: {
          this.Model.is_active_color = "danger";
          break;
        }

        default:
        this.Model.is_active_color = "active";
      }

    }
  }
}



