import { Injectable } from "@angular/core";
import { String } from "./String";

@Injectable()
export class MyAppSharedSettings {
  appName: string = "My Application Name";
  appTitle: string = "My Application Title";
  appVersion: string = "1.0.0.0 beta";
  welcomeMessage: string = "Welcome";
  userName: string = "";
  password: string = "";
  token:string="";
  ExpireDate:Date;
  ip: string = "localhost";
  port: string = "80";
  API_URL: string = String.Format(
    "http://{0}:{1}/sbpwebapi/api/",
    this.ip,
    this.port
  );
  is_url_forward: boolean = false;
  login_count:number=0;
  api_name:string;
  is_register:boolean=true;
  mobile_mode:boolean=true;
}



