import { SingleModelResponse } from "./SingleModelResponse";
import { QuoteColumnViewModel } from "../ViewModel/QuoteColumnViewModel";

export class QuoteColumnResponse extends SingleModelResponse<QuoteColumnViewModel> {
  Model: QuoteColumnViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new QuoteColumnViewModel();
      this.Model.schemaname=obj.Model.schemaname;
      this.Model.tablename=obj.Model.tablename;
      this.Model.columnname=obj.Model.columnname;
      this.Model.creator=obj.Model.creator;
      this.Model.create_time=obj.Model.create_time;
      this.Model.modifier=obj.Model.modifier;
      this.Model.last_update_time=obj.Model.last_update_time;

    }
  }
}



