import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { ProgramPageListResponse } from "../../Model/Response/ProgramPageListResponse";
import { ProgramPageResponse } from "../../Model/Response/ProgramPageResponse";
import { ProgramPageViewModel } from '../../Model/ViewModel/ProgramPageViewModel';

/*
  Generated class for the ProgramPageServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class ProgramPageServicesProvider {
  private ProgramPages: ProgramPageListResponse;
  private ProgramPage: ProgramPageResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "ProgramPage";
    console.log("Hello ProgramPageServiceProvider Provider");
  }
  public GetListsAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "ProgramPage?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.ProgramPages = new ProgramPageListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.ProgramPages;
    });
  }
  public GetPageListsAsync(PageSize: number, PageNumber: number, keyword: string,program_no:string,order_type:boolean) {
    var sub_url: string;
     sub_url = "ProgramPage?PageSize={1}&PageNumber={2}&keyword={3}&program_no={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,program_no,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.ProgramPages = new ProgramPageListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.ProgramPages;
    });
  }
  public GetSelectPageListsAsync(PageSize: number, PageNumber: number, keyword: string,menu_no:string,order_type:boolean) {
    var sub_url: string;
     sub_url = "ProgramPage?PageSize={1}&PageNumber={2}&keyword={3}&menu_no={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,menu_no,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.ProgramPages = new ProgramPageListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.ProgramPages;
    });
  }
  public GetAsync(ProgramPage_no: string) {
    var sub_url: string;
     sub_url = "ProgramPage/{1}";
    sub_url = String.Format(sub_url, this.ctl,ProgramPage_no);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.ProgramPage = new ProgramPageResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        Model: item.Model
      });
      return this.ProgramPage;
    });
  }

  public PostPutAsync(ProgramPageViewModel:ProgramPageViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "ProgramPage";
    else
      sub_url = "ProgramPage/" + ProgramPageViewModel.page_name ;
    return this.Services.PostPutAsync(sub_url, this.ctl, true,ProgramPageViewModel,mode).map(
      item => {
        this.ProgramPage = new ProgramPageResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.ProgramPage;
      }
    );
  }
  public PostAsync(ProgramPageViewModel:ProgramPageViewModel) {
    var sub_url: string;
    sub_url = "ProgramPage";
    return this.Services.PostAsync(sub_url, this.ctl, true,ProgramPageViewModel).map(
      item => {
        this.ProgramPage = new ProgramPageResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.ProgramPage;
      }
    );
  }
  public PutAsync(ProgramPageViewModel:ProgramPageViewModel) {
    var sub_url: string;
    sub_url = "ProgramPage/" + ProgramPageViewModel.page_name;
    return this.Services.PutAsync(sub_url, this.ctl, true,ProgramPageViewModel).map(
      item => {
        this.ProgramPage = new ProgramPageResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.ProgramPage;
      }
    );
  }
  public DeleteAsync(page_name:string) {
    var sub_url: string;
    sub_url = "ProgramPage/"+page_name;
    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.ProgramPage = new ProgramPageResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.ProgramPage;
      }
    );
  }




}



