import { SingleModelResponse } from "./SingleModelResponse";
import { JobProcessedSqlViewModel } from "../ViewModel/JobProcessedSqlViewModel";

export class JobProcessedSqlResponse extends SingleModelResponse<
  JobProcessedSqlViewModel
> {
  Model: JobProcessedSqlViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new JobProcessedSqlViewModel();
      this.Model.ssis_job_no = obj.Model.ssis_job_no; //SSIS作業編號
      this.Model.exec_status_key = obj.Model.exec_status_key; //執行狀態主鍵
      this.Model.sql_statement = (obj && obj.Model.sql_statement) || null; //sql語法
      this.Model.is_active = obj.Model.is_active; //啟用註記
      this.Model.creator = obj.Model.creator; //建立者
      this.Model.create_time = obj.Model.create_time; //建立時間
      this.Model.modifier = obj.Model.modifier; //最後更新者
      this.Model.last_update_time = obj.Model.last_update_time; //最後更新時間
    }
  }
}



