import { DirectoryBrowserViewModel } from "../ViewModel/DirectoryBrowserViewModel";
import { ListModelResponse } from "./ListModelResponse";

export class DirectoryBrowserListResponse extends ListModelResponse<
  DirectoryBrowserViewModel
> {
  Model: DirectoryBrowserViewModel[];
  constructor(obj?: any) {
    super(obj);
    this.Model = [];
    this.Message = obj.Messag;
    this.DidError = obj.DidError;
    this.ErrorMessage = (obj && obj.ErrorMessage) || null;
    this.PageNumber = obj.PageNumber;
    this.PageSize = obj.PageSize;
    this.PageRows = obj.PageRows;
    this.TotalPages = obj.TotalPages;
    this.TotalRows = obj.TotalRows;
    if (this.DidError === false) {
      if (obj.Model != null) {
        obj.Model.forEach(data => {
          let item = new DirectoryBrowserViewModel();
          item.directory_name = data.directory_name;
          item.filepath = data.filepath;

          item.create_time = data.create_time;
          item.file_cnt=data.file_cnt;
          item.sub_directory_cnt=data.sub_directory_cnt;
          if(item.file_cnt>0)
            item.has_file=true;
          else
            item.has_file=false;
          switch (item.has_file) {
            case true: {
              item.has_file_color = "primary";
              break;
            }

            default:
              item.has_file_color = "primary";
          }
          this.Model.push(item);
        }) || null;
      }
    }
  }
}



