import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { ProgramListResponse } from "../../Model/Response/ProgramListResponse";
import { ProgramResponse } from "../../Model/Response/ProgramResponse";
import { ProgramViewModel } from '../../Model/ViewModel/ProgramViewModel';
import { StringResponse } from "../../Model/Response/StringResponse";

/*
  Generated class for the ProgramServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class ProgramServicesProvider {
  private Programs: ProgramListResponse;
  private Program: ProgramResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "Program";
    console.log("Hello ProgramServiceProvider Provider");
  }
  public GetListsAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "Program?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Programs = new ProgramListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Programs;
    });
  }
  public GetIonicListsAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "Program?PageSize={1}&PageNumber={2}&keyword={3}&program_no={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,"ION",order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Programs = new ProgramListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Programs;
    });
  }
  public GetSqlListsAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "Program?PageSize={1}&PageNumber={2}&keyword={3}&program_no={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,"SQL",order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Programs = new ProgramListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Programs;
    });
  }
  public GetAsync(Program_no: string) {
    var sub_url: string;
     sub_url = "Program/{1}";
    sub_url = String.Format(sub_url, this.ctl,Program_no);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Program = new ProgramResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        Model: item.Model
      });
      return this.Program;
    });
  }
  public GetAddSelectListsByPermissionAsync(PageSize: number, PageNumber: number, keyword: string,permission_no:string,order_type:boolean) {
    var sub_url: string;
     sub_url = "Program?PageSize={1}&PageNumber={2}&keyword={3}&permission_no={4}&order_type={5}&bus_type_key=022|010&program_type_key=013|ION";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,permission_no,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Programs = new ProgramListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Programs;
    });
  }
  public GetAddSelectListsByPageAsync(PageSize: number, PageNumber: number, keyword: string,page_name:string,order_type:boolean) {
    var sub_url: string;
     sub_url = "Program?PageSize={1}&PageNumber={2}&keyword={3}&page_name={4}&order_type={5}&bus_type_key=022|010&program_type_key=013|ION";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,page_name,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Programs = new ProgramListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Programs;
    });
  }
  public PostPutAsync(ProgramViewModel:ProgramViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "Program";
    else
      sub_url = "Program/" + ProgramViewModel.program_no ;
    return this.Services.PostPutAsync(sub_url, this.ctl, true,ProgramViewModel,mode).map(
      item => {
        this.Program = new ProgramResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.Program;
      }
    );
  }

  public DeleteAsync(program_no:string) {
    var sub_url: string;
    sub_url = "Program/"+program_no;
    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.Program = new ProgramResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.Program;
      }
    );
  }
  public GetSqlAsync(program_no:string,method:string) {
    var sub_url: string;
    sub_url = "Program/get_{2}_sql/{1}";
    sub_url = String.Format(sub_url, this.ctl,program_no,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }



}



