import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileBrowserListResponse } from "../../Model/Response/FileBrowserListResponse";
import { FileBrowserResponse } from "../../Model/Response/FileBrowserResponse";
import { FtpFileBrowserViewModel } from "../../Model/ViewModel/FtpFileBrowserViewModel";

/*
  Generated class for the FileBrowserServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FtpFileBrowserServicesProvider {
  private FileBrowsers: FileBrowserListResponse;
  private FileBrowser: FileBrowserResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "FileBrowser";
    console.log("Hello FileBrowserServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,path:string,order_type:boolean,remote_conn_id:string, conn_type_key:string,encrypt_method_key:string) {
    var sub_url: string;
     sub_url = "FtpFileBrowser?PageSize={1}&PageNumber={2}&keyword={3}&path={4}&order_type={5}&remote_conn_id={6}&conn_type_key={7}&encrypt_method_key={8}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,path,order_type,remote_conn_id,conn_type_key,encrypt_method_key);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileBrowsers = new FileBrowserListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileBrowsers;
    });
  }

  public Download(path:string,local_filepath:string,filename:string,remote_conn_id:string, conn_type_key:string,encrypt_method_key:string) {
    var sub_url: string;
    sub_url = "FtpFileBrowser/Download?path={1}&remote_conn_id={2}&conn_type_key={3}&encrypt_method_key={4}&local_filepath={5}&filename={6}";
    sub_url = String.Format(sub_url, this.ctl,path,remote_conn_id,conn_type_key,encrypt_method_key,local_filepath,filename);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        this.FileBrowser = new FileBrowserResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileBrowser;
      }
    );
  }
  public Upload(FtpFileBrowserViewModel) {
    var sub_url: string;

      sub_url = "FtpFileBrowser";

    sub_url = String.Format(sub_url, this.ctl);
    return this.Services.PostAsync(sub_url, this.ctl, true,FtpFileBrowserViewModel).map(
      item => {
        this.FileBrowser = new FileBrowserResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.FileBrowser;
      }
    );
  }

  public PutAsync(filefullname:string,FtpFileBrowserViewModel:FtpFileBrowserViewModel) {
    var sub_url: string;
    //mode:Rename或Copy
    sub_url = "FtpFileBrowser?filefullname={1}";
    sub_url = String.Format(sub_url, this.ctl,filefullname);
    return this.Services.PutAsync(sub_url, this.ctl, true,FtpFileBrowserViewModel).map(
      item => {
        return new FileBrowserResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

      }
    );
  }


  public DeleteAsync(path:string,filename:string,remote_conn_id:string, conn_type_key:string,encrypt_method_key:string) {
    var sub_url: string;
    sub_url = "FtpFileBrowser?path={1}&remote_conn_id={2}&conn_type_key={3}&encrypt_method_key={4}&filename={5}";
    sub_url = String.Format(sub_url, this.ctl,path,remote_conn_id,conn_type_key,encrypt_method_key,filename);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.FileBrowser = new FileBrowserResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileBrowser;
      }
    );
  }
}



