import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileManagementListResponse } from "../../Model/Response/FileManagementListResponse";
import { FileManagementResponse } from "../../Model/Response/FileManagementResponse";
import { FileManagementViewModel } from '../../Model/ViewModel/FileManagementViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FileManagementServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileManagementServicesProvider extends FileServicesProvider {
  private FileManagements: FileManagementListResponse;
  private FileManagement: FileManagementResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FileManagement";
    console.log("Hello FileManagementServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,exec_file_seq:number,is_directory:boolean,order_type:boolean) {
    var sub_url: string;
    if(exec_file_seq===0)
    {
      sub_url = "FileManagement?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&is_directory={5}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,is_directory,order_type);
    }
    else
    {
      sub_url = "FileManagement/Select?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&exec_file_seq={5}&is_directory={6}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,exec_file_seq,is_directory,order_type);
    }
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileManagements = new FileManagementListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileManagements;
    });
  }



  public PostPutAsync(FileManagementViewModel:FileManagementViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileManagement";
    else
    sub_url = "FileManagement/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileManagementViewModel.exec_file_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileManagementViewModel,mode).map(
      item => {
        this.FileManagement = new FileManagementResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileManagement;
      }
    );
  }
}



