export class TableViewModel {
  /// <summary>
///  結構描述
/// <summary>
public schemaname: string;
/// <summary>
///  資料表
/// <summary>
public tablename: string;
/// <summary>
///  檔案群組
/// <summary>
public file_group: string;
/// <summary>
///  建立者
/// <summary>
public creator: string;
/// <summary>
///  建立時間
/// <summary>
public create_time: Date;
/// <summary>
///  最後更新者
/// <summary>
public modifier: string;
/// <summary>
///  最後更新時間
/// <summary>
public last_update_time: Date;
/// <summary>
///  資料表類別主鍵
/// <summary>
public table_cat_key: string;

/// <summary>
///  資料表說明
/// <summary>
public tablecname: string;
/// <summary>
///  資料表類別
/// <summary>
public table_cat_desc: string;
/// <summary>
///  資料表全名
/// <summary>
public tablefullname: string;

/// <summary>
///  異常欄位
/// <summary>
public err_columnname: string;

/// <summary>
///  代碼檢核筆數
/// <summary>
code_check_cnt:number;
/// <summary>
///  代碼檢核顏色
/// <summary>
code_check_color:string;

/// <summary>
///  雙引號筆數
/// <summary>
quote_column_cnt:number;
/// <summary>
///  雙引號顏色
/// <summary>
quote_column_color:string;
}



