import { ListModelResponse } from "./ListModelResponse";
import { ProgramPageMenuViewModel } from "../ViewModel/ProgramPageMenuViewModel";
import { ProgramPageViewModel } from "../ViewModel/ProgramPageViewModel";
import { MenuViewModel } from "../ViewModel/MenuViewModel";

/**
 * 回傳IQueryable物件內容及訊息
 *
 * @public
 * @class ListModelResponse
 * @implements  IListModelResponse
 * @param   {Function}    [name]
 */
export class ProgramPageMenuListResponse extends ListModelResponse<
  ProgramPageMenuViewModel
> {
  Model: ProgramPageMenuViewModel[];
  constructor(obj?: any) {
    super(obj);
    this.Model = [];
    this.Message = obj.Messag;
    this.DidError = obj.DidError;
    this.ErrorMessage = (obj && obj.ErrorMessage) || null;
    this.PageNumber = obj.PageNumber;
    this.PageSize = obj.PageSize;
    this.PageRows = obj.PageRows;
    this.TotalPages = obj.TotalPages;
    this.TotalRows = obj.TotalRows;

    if (obj.DidError === false) {
      obj.Model.forEach(data => {
        let item = new ProgramPageMenuViewModel();
        item.page_name=data.page_name;
        item.menu_no=data.menu_no;
        item.display_order_no=data.display_order_no;
        item.creator=data.creator;
        item.create_time=data.create_time;
        item.modifier=data.modifier;
        item.last_update_time=data.last_update_time;
        let pg=new ProgramPageViewModel();
        pg.program_no=data.program_pageViewModel.program_no;
        pg.page_name=data.program_pageViewModel.page_name;
        pg.page_desc=data.program_pageViewModel.page_desc;
        item.ProgramPageViewModel=pg;
        let m=new MenuViewModel();
        m.menu_no=data.menuViewModel.menu_no;
        m.menu_desc=data.menuViewModel.menu_desc;
        item.ProgramPageViewModel=pg;
        item.MenuViewModel=m;
        this.Model.push(item);
      }) || null;
    }
  }
}



