export class CodeCheckViewModel {
  /// <summary>
  ///  結構描述
  /// <summary>
  public schemaname: string;
  /// <summary>
  ///  資料表
  /// <summary>
  public tablename: string;
  /// <summary>
  ///  欄位名稱
  /// <summary>
  public columnname: string;
  /// <summary>
  ///  檢核類別
  /// <summary>
  public check_type: string;
  /// <summary>
  ///  啟用註記
  /// <summary>
  public is_active: boolean;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;
  /// <summary>
  ///  啟用說明
  /// <summary>
  public is_active_desc: string;

  /// <summary>
  ///  啟用顏色
  /// <summary>
  public is_active_color: string;
}



