import { SchListResponse } from "./../../Model/Response/SchListResponse";
import { String } from "./../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { StringResponse } from "../../Model/Response/StringResponse";

/*
  Generated class for the SchServicesProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class SchServicesProvider {
  ctl: string;
  Sch: SchListResponse;
  constructor(
    public Services: ServicesProvider
  ) {
    this.ctl = "Sch";
    console.log("Hello SchServicesProvider Provider");
  }

  // GetListsAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
  //   var sub_url: string;
  //    sub_url = "Sch?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
  //   sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
  //   return this.Services.GetAsync(sub_url,this.ctl,true)
  //     .map(item => {
  //       this.Sch = new SchListResponse({
  //         Message: item.Message,
  //         DidError: item.DidError,
  //         ErrorMessage: item.ErrorMessage,
  //         PageSize: item.PageSize,
  //         PageNumber: item.PageNumber,
  //         TotalRows: item.TotalRows,
  //         PageRows: item.PageRows,
  //         TotalPages: item.TotalPages,
  //         Model: item.Model
  //       });
  //       return this.Sch;
  //     });
  // }

  GetSelectListsAsync(PageSize: number, PageNumber: number, keyword: string,ssis_job_no:string,order_type:boolean) {
    var sub_url: string;
     sub_url = "Sch?PageSize={1}&PageNumber={2}&keyword={3}&ssis_job_no={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,ssis_job_no,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true)
      .map(item => {
        this.Sch = new SchListResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          PageSize: item.PageSize,
          PageNumber: item.PageNumber,
          TotalRows: item.TotalRows,
          PageRows: item.PageRows,
          TotalPages: item.TotalPages,
          Model: item.Model
        });
        return this.Sch;
      });
  }
  public GetSqlAsync(sch_no:string,method:string) {
    var sub_url: string;
    sub_url = "Sch/get_{2}_sql?sch_no={1}";
    sub_url = String.Format(sub_url, this.ctl,sch_no,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
  public DeleteAsync(sch_no:string) {
    var sub_url: string;
    sub_url = "Sch/{1}";
    sub_url = String.Format(sub_url, this.ctl,sch_no);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        return new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: "刪除"
        });

      }
    );
  }
}



