export class JobProcessedSqlViewModel {
  /// <summary>
  ///  SSIS作業編號
  /// <summary>
  public ssis_job_no: string;
  /// <summary>
  ///  執行狀態主鍵
  /// <summary>
  public exec_status_key: string;
  /// <summary>
  ///  sql語法
  /// <summary>
  public sql_statement: string;
  /// <summary>
  ///  啟用註記
  /// <summary>
  public is_active: boolean;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;
  /// <summary>
  ///  執行狀態
  /// <summary>
  public exec_status: string;
  /// <summary>
  ///  執行狀態
  /// <summary>
  public exec_status_desc: string;
  /// <summary>
  ///  執行狀態顏色
  /// <summary>
  public exec_status_color: string;
  /// <summary>
  ///  啟用註記顏色
  /// <summary>
  public is_active_color: string;
  /// <summary>
  ///  啟用註記
  /// <summary>
  public is_active_desc: string;
}



