import { SingleModelResponse } from "./SingleModelResponse";
import { TableCreateExtViewModel } from "../ViewModel/TableCreateExtViewModel";

export class TableCreateExtResponse extends SingleModelResponse<
  TableCreateExtViewModel
> {
  Model: TableCreateExtViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new TableCreateExtViewModel();
      this.Model.schemaname = obj.Model.schemaname; //結構描述
      this.Model.tablename = obj.Model.tablename; //資料表
      this.Model.create_sql = (obj && obj.Model.create_sql) || null; //create_sql
      this.Model.create_fk_sql = (obj && obj.Model.create_fk_sql) || null; //create_fk_sql
      this.Model.create_depend_fk_sql =
        (obj && obj.Model.create_depend_fk_sql) || null; //create_depend_fk_sql
      this.Model.drop_depend_fk_sql =
        (obj && obj.Model.drop_depend_fk_sql) || null; //drop_depend_fk_sql
        this.Model.create_index_sql =
        (obj && obj.Model.create_index_sql) || null; //drop_depend_fk_sql
      this.Model.create_date = (obj && obj.Model.create_date) || null; //建立日期
      this.Model.modify_date = (obj && obj.Model.modify_date) || null; //modify_date
    }
  }
}



