import {StringResponse} from '../../Model/Response/StringResponse';
import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { QuoteColumnListResponse } from "../../Model/Response/QuoteColumnListResponse";
import { QuoteColumnResponse } from "../../Model/Response/QuoteColumnResponse";
import { QuoteColumnViewModel } from '../../Model/ViewModel/QuoteColumnViewModel';

/*
  Generated class for the QuoteColumnServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class QuoteColumnServicesProvider {
  private QuoteColumns: QuoteColumnListResponse;
  private QuoteColumn: QuoteColumnResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "QuoteColumn";
    console.log("Hello QuoteColumnServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number,schemaname:string,tablename:string, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "QuoteColumn?PageSize={1}&PageNumber={2}&keyword={3}&schemaname={4}&tablename={5}&order_type={6}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,schemaname,tablename,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.QuoteColumns = new QuoteColumnListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.QuoteColumns;
    });
  }



  public PostPutAsync(QuoteColumnViewModel:QuoteColumnViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "QuoteColumn";
    else
    sub_url = "QuoteColumn?schemaname={1}&tablename={2}&columnname={3}";
    sub_url = String.Format(sub_url, this.ctl,QuoteColumnViewModel.schemaname,QuoteColumnViewModel.tablename,QuoteColumnViewModel.columnname);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,QuoteColumnViewModel,mode).map(
      item => {
        this.QuoteColumn = new QuoteColumnResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.QuoteColumn;
      }
    );
  }

  public DeleteAsync(schemaname:string,tablename:string,columnname:string) {
    var sub_url: string;
    sub_url = "QuoteColumn?schemaname={1}&tablename={2}&columnname={3}";
    sub_url = String.Format(sub_url, this.ctl,schemaname,tablename,columnname);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.QuoteColumn = new QuoteColumnResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.QuoteColumn;
      }
    );
  }



  public GetSqlAsync(schemaname:string,tablename:string,columnname:string,method:string) {
    var sub_url: string;
    sub_url = "QuoteColumn/get_{4}_sql?schemaname={1}&tablename={2}&columnname={3}";
    sub_url = String.Format(sub_url, this.ctl,schemaname,tablename,columnname,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
}



