import { SingleModelResponse } from "./SingleModelResponse";
import { TableDetailViewModel } from "../ViewModel/TableDetailViewModel";

export class TableDetailResponse extends SingleModelResponse<
  TableDetailViewModel
> {
  Model: TableDetailViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new TableDetailViewModel();
      this.Model.schemaname = obj.Model.schemaname;
      this.Model.tablename = obj.Model.tablename;
      this.Model.tablecname = obj.Model.tablecname;
      this.Model.columncname = obj.Model.columncname;
      this.Model.column_id = obj.Model.column_id;
      this.Model.col_index = obj.Model.col_index;
      this.Model.columnname = obj.Model.columnname;
      this.Model.data_type = obj.Model.data_type;
      this.Model.is_pk = obj.Model.is_pk;
      this.Model.is_uni_cluster = obj.Model.is_uni_cluster;
      this.Model.is_cluster = obj.Model.is_cluster;
      this.Model.is_nullable = obj.Model.is_nullable;
      this.Model.is_nullable_str=obj.Model.is_nullable?"Y":"N";
      this.Model.default_value = obj.Model.default_value;
      this.Model.computecolumn = obj.Model.computecolumn;
      this.Model.is_identity = obj.Model.is_identity;
      this.Model.is_identity_str = obj.Model.is_identity?"Y":"N";
      this.Model.memo = obj.Model.memo;
      this.Model.id_check= obj.Model.id_check;
      this.Model.del_flag=obj.Model.del_flag;
      this.Model.col_start_loc=obj.Model.col_start_loc;
      this.Model.col_length=obj.Model.col_length;
      this.Model.column_type_key=obj.Model.column_type_key;
      this.Model.column_type=obj.Model.column_type;
      this.Model.history_column_flag=obj.Model.history_column_flag;
      this.Model.date_convert_format=obj.Model.date_convert_format;
      this.Model.is_sign=obj.Model.is_sign;
      this.Model.tablefullname=obj.Model.schemaname+'.' +obj.Model.tablename;
      this.Model.check_type=obj.Model.check_type;
      if (this.Model.check_type !=null) this.Model.code_check_color = "danger-left";
      else this.Model.code_check_color = "light";
    }
  }
}



